our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @px: (@val * 10); // Convert rem values to px equilavent\n            @unitless: unit(@px);\n\n            //  ...and less than 0\n            & when (@unitless < 0) {\n                @abs: abs(@unitless);\n                @class-name: ~\"@{class-prefix}n@{abs}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n\n            //  ...and equal to or greater than 0\n            & when (@unitless >= 0) {\n                @class-name: ~\"@{class-prefix}@{unitless}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, @index + 1, @values);\n    }\n\n    #auto-generate-no-convert(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index-no-convert(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, @index + 1, @values);\n    }\n\n\n    //  ========================================================================\n    //  $   CREATE FONT FACE\n    //  ------------------------------------------------------------------------\n    #font-face(@type, @name, @style: normal, @local: false, @ascent: normal) {\n        #d-internal-config();\n\n        if((@generate-font-face = true), each(@type, {\n          @font-face {\n            font-style: @style;\n            font-weight: @key;\n            font-family: @name;\n            src: if((@local = true ), local(@value), url(\"@{define-font-path}@{value}.woff2\") format(\"woff2\"));\n            ascent-override: @ascent;\n          };\n        }));\n    }\n\n    #gradient-classes(@color) {\n      //  COLOR STOP 1, STARTING STOP\n      .d-bgg-from-@{color},\n      .h\\:d-bgg-from-@{color}:hover,\n      .f\\:d-bgg-from-@{color}:focus {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .f\\:d-bgg-from-@{color}:focus-within {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .fv\\:d-bgg-from-@{color}:focus-visible {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{color}', {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      });\n\n      //  COLOR STOP 2, ENDING STOP\n      .d-bgg-to-@{color},\n      .h\\:d-bgg-to-@{color}:hover,\n      .f\\:d-bgg-to-@{color}:focus {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .f\\:d-bgg-to-@{color}:focus-within {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .fv\\:d-bgg-to-@{color}:focus-visible {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-to-@{color}', {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      });\n    }\n\n    #gradient-stops(@color) {\n      #d-internals #gradient-classes(~\"@{color}-600\");\n      #d-internals #gradient-classes(~\"@{color}-500\");\n      #d-internals #gradient-classes(~\"@{color}-400\");\n      #d-internals #gradient-classes(~\"@{color}-300\");\n      #d-internals #gradient-classes(~\"@{color}-200\");\n      #d-internals #gradient-classes(~\"@{color}-100\");\n    }\n\n    #build-flex-columns(@num, @count: 1) when (@count =< @num) {\n    .d-fl-col@{count} {\n      display: flex;\n\n      //  Automatically make all direct child to obey the parent class\n      > * {\n        --fl-gap: 0;\n        --fl-basis: calc(100% / @count);\n\n        display: inline-flex;\n        margin: var(--fl-gap);\n        flex: 1 calc(var(--fl-basis) - ((var(--fl-gap) * 2)));\n\n        //  Add in a gap to items after the first row\n        &:nth-child(-n + @{count}) {\n          margin-top: 0;\n        }\n      }\n\n      //  If flex column is in a column direction (and became rows),\n      //  change the margin direction.\n      &.d-fd-column > * {\n        margin: var(--fl-gap) 0;\n      }\n    }\n\n    //  Iterate our loop until we hit our grid column total\n    #d-internals #build-flex-columns(@num, @count + 1);\n  }\n\n    //  ========================================================================\n    //  $   SPACING CLASSES\n    //  ------------------------------------------------------------------------\n    #spacing-classes(@BU, @prefix, @suffix: '') {\n      @stop-100: ~\"@{prefix}-100@{suffix}\";\n      @stop-200: ~\"@{prefix}-200@{suffix}\";\n      @stop-300: ~\"@{prefix}-300@{suffix}\";\n      @stop-400: ~\"@{prefix}-400@{suffix}\";\n      @stop-500: ~\"@{prefix}-500@{suffix}\";\n      @stop-600: ~\"@{prefix}-600@{suffix}\";\n      @stop-700: ~\"@{prefix}-700@{suffix}\";\n      @stop-800: ~\"@{prefix}-800@{suffix}\";\n\n      body {\n        --@{stop-100}:       calc(@BU / pow(2, 3));\n        --@{stop-200}:       calc(@BU / pow(2, 2));\n        --@{stop-300}:       calc(@BU / pow(2, 1));\n        --@{stop-400}:       @BU;\n        --@{stop-500}:       calc(@BU * pow(2, 1));\n        --@{stop-600}:       calc(@BU * pow(2, 2));\n        --@{stop-700}:       calc(@BU * pow(2, 3));\n        --@{stop-800}:       calc(@BU * pow(2, 4));\n      }\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: Avatars\n//\n//  These are avatars classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/avatars\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//    • SIZES\n//    • COLORS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-avatar {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --avatar-color-background: var(--gold-100);\n  --avatar-color-text: var(--fc-secondary);\n  --avatar-gradient-angle: 180deg;\n  --avatar-gradient-stop-1: var(--gold-100);\n  --avatar-gradient-stop-2: var(--magenta-100);\n  --avatar-gradient-stop-3: var(--blue-200);\n  --avatar-gradient:\n    conic-gradient(\n      from var(--avatar-gradient-angle) at 50% 50%,\n      var(--avatar-gradient-stop-1) 0deg,\n      var(--avatar-gradient-stop-2) 180deg,\n      var(--avatar-gradient-stop-3) 360deg\n    );\n  --avatar-size-shape: var(--size-600);\n  --avatar-size-text: var(--fs-200);\n  --avatar-presence-position-right: calc(var(--space-200) * -1); // -2\n  --avatar-presence-position-bottom: calc(var(--space-200) * -1); // -2\n\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: var(--avatar-size-shape);\n  height: var(--avatar-size-shape);\n  color: var(--avatar-color-text);\n  font-weight: var(--fw-bold);\n  font-size: var(--avatar-size-text);\n  line-height: var(--lh-100);\n  text-transform: uppercase;\n  background-color: var(--avatar-color-background);\n  background-image: var(--avatar-gradient);\n  border-radius: var(--br-pill);\n\n  &__image {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: inherit;\n  }\n\n  &__presence {\n    position: absolute;\n    right: var(--avatar-presence-position-right);\n    bottom: var(--avatar-presence-position-bottom);\n    display: flex;\n  }\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n\n  &--xs {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-200)); // 18\n    --avatar-presence-position-right: calc(var(--space-300) * -1); // -4\n    --avatar-presence-position-bottom: calc(var(--space-300) * -1); // -4\n  }\n\n  &--sm {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-400)); // 24\n    --avatar-size-text: var(--fs-100);\n    --avatar-presence-position-right: calc(var(--space-200) * -1); // -2\n    --avatar-presence-position-bottom: calc(var(--space-200) * -1); // -2\n  }\n\n  &--md {\n    --avatar-size-shape: var(--size-600); // 32\n    --avatar-size-text: var(--fs-200);\n    --avatar-presence-position-right: calc(var(--space-100) * -1); // -1\n    --avatar-presence-position-bottom: calc(var(--space-100) * -1); // -1\n  }\n\n  &--lg {\n    --avatar-size-shape: calc(var(--size-600) + var(--size-500)); // 48\n    --avatar-size-text: var(--fs-300);\n    --avatar-presence-position-right: var(--space-100); // 1\n    --avatar-presence-position-bottom: var(--space-100); // 1\n  }\n\n  &--xl {\n    --avatar-size-shape: var(--size-700); // 64\n    --avatar-size-text: var(--fs-400);\n    --avatar-presence-position-right: var(--space-300); // 4\n    --avatar-presence-position-bottom: var(--space-300); // 4\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: BADGES\n//\n//  These are badge classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/badges\n//\n//  TABLE OF CONTENTS\n//  • COMPONENT CSS VARIABLES\n//  • BASE STYLE\n//  • KIND\n//  • TYPE\n//  • SLOTS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n\n.d-badge {\n  //  COMPONENT CSS VARIABLES\n  //  --------------------------------------------------------------------------\n  --badge-color-text: var(--fc-primary);\n  --badge-color-background: var(--black-200);\n  --badge-color-background-ai: linear-gradient(to bottom right, var(--purple-400) 0%, var(--magenta-300) 100%);\n  --badge-radius: var(--size-300);\n  --badge-line-height: calc(var(--size-500) + var(--size-200));\n  --badge-font-size: var(--fs-100);\n  --badge-font-weight: var(--fw-semibold);\n  --badge-gap: var(--space-300);\n  --badge-letter-spacing: calc(var(--size-100) / 2);\n  --badge-padding-y: var(--space-100);\n  --badge-padding-x: var(--space-400);\n  --badge-text-case: none;\n\n  //  BASE STYLE\n  //  --------------------------------------------------------------------------\n  display: inline-flex;\n  gap: var(--badge-gap);\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n  min-width: calc(var(--size-400) + var(--size-500));\n  padding: var(--badge-padding-y) var(--badge-padding-x);\n  color: var(--badge-color-text);\n  font-weight: var(--badge-font-weight);\n  font-size: var(--badge-font-size);\n  line-height: var(--badge-line-height);\n  letter-spacing: var(--badge-letter-spacing);\n  text-align: center;\n  text-transform: var(--badge-text-case);\n  background-color: var(--badge-color-background);\n  border-radius: var(--badge-radius);\n\n  //  Kind\n  //  --------------------------------------------------------------------------\n\n  &--count {\n    --badge-radius: var(--br-pill);\n    --badge-padding-x: calc(var(--space-400) - var(--space-100));\n    --badge-padding-y: var(--space-300);\n    --badge-line-height: var(--size-500);\n  }\n\n  //  TYPE\n  //  --------------------------------------------------------------------------\n\n  &--info {\n    --badge-color-background: var(--bgc-info);\n  }\n\n  &--success {\n    --badge-color-background: var(--bgc-success);\n  }\n\n  &--warning {\n    --badge-color-background: var(--bgc-warning);\n  }\n\n  &--critical {\n    --badge-color-background: var(--bgc-critical);\n  }\n\n  &--bulletin {\n    --badge-color-text: var(--white);\n    --badge-color-background: var(--purple-400);\n  }\n\n  &--ai {\n    --badge-color-text: var(--white);\n    --badge-color-background: var(--magenta-300);\n\n    background-image: var(--badge-color-background-ai);\n  }\n\n  //  SLOTS\n  //  --------------------------------------------------------------------------\n\n  &__label {\n    display: flex;\n  }\n\n  &__icon-left,\n  &__icon-right {\n    display: flex;\n  }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BANNERS\n//\n//  These are the styles for banners, which are a type of notice used\n//  to alert users about important information relevant to them.\n//  For further documentation of these styles, please visit:\n//  https://dialpad.design/components/banners\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n.d-banner {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --banner-color-background: var(--bgc-secondary);\n    --banner-color-text: var(--fc-primary);\n    --notice-color-icon: var(--toast-color-text);\n    --banner-color-border: 1px solid hsla(var(--black-900-hsl) ~' / ' 10%);\n    --banner-font-size: var(--fs-200);\n    --banner-line-height: var(--lh-200);\n    --banner-dialog-padding-y: var(--space-400);\n    --banner-dialog-padding-x: var(--space-500);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    left: 0;\n    z-index: var(--zi-navigation-fixed);\n    display: flex;\n    box-sizing: border-box;\n    width: 100%;\n    min-height: calc(var(--size-300) * 12); // 48\n    color: var(--banner-color-text);\n    font-size: var(--banner-font-size);\n    line-height: var(--banner-line-height);\n    background-color: var(--banner-color-background);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: calc(var(--size-3) * 16); // -64\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--banner-dialog-padding-y) var(--banner-dialog-padding-x);\n    border-bottom: var(--banner-color-border);\n\n    .d-notice__content {\n        flex-direction: row;\n        gap: var(--space-300);\n        align-items: baseline;\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner-color-background: var(--bgc-contrast);\n    --banner-color-text: var(--black-100);\n    --notice-color-icon: var(--black-100);\n    --banner-color-border: transparent;\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner-color-background: var(--bgc-critical);\n\n    &.d-banner--important {\n        --banner-color-background: var(--red-400);\n        --banner-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-banner--info {\n    --banner-color-background: var(--bgc-info);\n\n    &.d-banner--important {\n        --banner-color-background: var(--blue-400);\n        --banner-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-banner--success {\n    --banner-color-background: var(--bgc-success);\n\n    &.d-banner--important {\n        --banner-color-background: var(--green-400);\n        --banner-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-banner--warning {\n    --banner-color-background: var(--bgc-warning);\n\n    &.d-banner--important {\n        --banner-color-background: var(--gold-200);\n        --banner-color-text: var(--fc-primary);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: BREADCRUMBS\n//\n//  These are breadcrumb classes for Dialpad's design system Dialtone.\n//  For further information, visit https://dialpad.design/components/breadcrumbs\n//\n//  TABLE OF CONTENTS\n//  • WRAPPER\n//  • ITEMS & LINKS\n//  • INVERTED STYLE\n//\n//  ============================================================================\n//  $  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs > ol {\n    display: flex;\n    flex-wrap: wrap;\n    align-items: flex-start;\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    list-style: none;\n}\n\n//  ============================================================================\n//  $  ITEMS & LINKS\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs {\n    //  --  INDIVIDUAL ITEMS\n    .d-breadcrumbs__item {\n        position: relative;\n        margin: 0;\n        margin-left: var(--space-400);\n        padding: 0;\n        list-style: none;\n\n        //  Provide a forward slash with each element except when it's the last one.\n        &:not(:last-of-type) {\n            margin-right: calc(var(--space-400) + var(--space-300));\n\n            &::before {\n                position: absolute;\n                right: calc(calc(var(--space-400) + var(--space-300)) * -1);\n                margin-top: calc(var(--space-100) * -1);\n                color: var(--fc-tertiary);\n                content: '/';\n            }\n        }\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        text-decoration: none;\n        cursor: default;\n    }\n}\n\n\n//  ============================================================================\n//  $  INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-breadcrumbs.d-breadcrumbs--inverted {\n    //  --  ITEM\n    .d-breadcrumbs__item:not(:last-of-type)::before {\n        color: var(--fc-primary-inverted);\n    }\n\n    //  --  SELECTED STATE (CURRENT PAGE)\n    .d-breadcrumbs__item--selected .d-link {\n        &:hover {\n            color: var(--fc-primary-inverted);\n        }\n    }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BUTTONS\n//\n//  These are button classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/buttons\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • VISUAL STYLES\n//  • ALTERNATE STYLES\n//  • SIZES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n\n.d-btn,\n.d-btn--md {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --button-color-text: var(--purple-400);\n    --button-color-background: transparent;\n    --button-color-border: transparent;\n    --button-gap: calc(var(--button-padding-x) / 2);\n    --button-border-radius: var(--size-400);\n    --button-border-width: var(--size-100);\n    --button-font-size: var(--fs-200);\n    --button-font-weight: var(--fw-medium);\n    --button-line-height-xs: var(--lh-200); // TODO: these used to vary, remove if all wind up all the same\n    --button-line-height-sm: var(--lh-200); // TODO: these used to vary, remove if all wind up all the same\n    --button-line-height-md: var(--lh-200); // TODO: these used to vary, remove if all wind up all the same\n    --button-line-height-lg: var(--lh-200); // TODO: these used to vary, remove if all wind up all the same\n    --button-line-height-xl: var(--lh-200); // TODO: these used to vary, remove if all wind up all the same\n    --button-line-height: var(--button-line-height-md);\n    --button-padding-y-xs: calc(calc(var(--space-400) - var(--space-100)) - var(--button-border-width)); // ((8 - 1) - border-width)\n    --button-padding-x-xs: calc(var(--space-400) - var(--button-border-width)); // 8 minus border-width\n    --button-padding-y-sm: calc(var(--space-400) - var(--button-border-width)); // 8 minus border-width\n    --button-padding-x-sm: calc((var(--space-500) - var(--space-300)) - var(--button-border-width)); // 12 minus border-width\n    --button-padding-y-md: calc(var(--space-400) - var(--button-border-width)); // 8 minus border-width\n    --button-padding-x-md: calc((var(--space-500) - var(--space-300)) - var(--button-border-width)); // 12 minus border-width\n    --button-padding-y-lg: calc((var(--space-400) + var(--space-200)) - var(--button-border-width)); // 10 minus border-width\n    --button-padding-x-lg: calc(var(--space-500) - var(--button-border-width)); // 16 minus border-width\n    --button-padding-y-xl: calc((var(--space-500) - var(--space-300)) - var(--button-border-width)); // 12 minus border-width\n    --button-padding-x-xl: calc(var(--space-500) - var(--button-border-width)); // 16 minus border-width\n    --button-padding-y: var(--button-padding-y-md);\n    --button-padding-x: var(--button-padding-x-md);\n\n    position: relative;\n    display: inline-flex;\n    gap: var(--button-gap);\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    padding: var(--button-padding-y) var(--button-padding-x);\n    color: var(--button-color-text);\n    font-weight: var(--button-font-weight);\n    font-size: var(--button-font-size);\n    font-family: inherit;\n    line-height: var(--button-line-height);\n    text-transform: inherit;\n    text-decoration: none;\n    vertical-align: middle;\n    background-color: var(--button-color-background);\n    border-color: var(--button-color-border);\n    border-style: solid;\n    border-width: var(--button-border-width);\n    border-radius: var(--button-border-radius);\n    box-shadow: var(--button--bs);\n    cursor: pointer;\n    transition-timing-function: var(--ttf-out-quint);\n    transition-duration: var(--td300);\n    transition-property: background-color, border, box-shadow;\n    user-select: none;\n    fill: currentColor;\n\n    //  --  STATES\n    //  ------------------------------------------------------------------------\n    &:hover:not([disabled]) {\n        --button-color-text: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n        --button-color-background: hsla(var(--purple-400-hsl) ~' / ' 4%);\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n        --button-color-text: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n        --button-color-background: hsla(var(--purple-400-hsl) ~' / ' 14%);\n    }\n\n\n    &:focus-visible {\n      outline: none;\n      box-shadow: var(--bs-focus-ring);\n    }\n\n    &[disabled] {\n        --button-color-border: transparent !important;\n        --button-color-text: var(--fc-disabled) !important;\n        --button-color-background: var(--black-300) !important;\n\n        cursor: not-allowed;\n        transition: none;\n    }\n\n    .d-btn__icon .d-svg {\n        width: @icon16;\n        height: @icon16;\n    }\n}\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--xs {\n    --button-padding-y: var(--button-padding-y-xs);\n    --button-padding-x: var(--button-padding-x-xs);\n    --button-font-size: var(--fs-100);\n    --button-line-height: var(--button-line-height-xs);\n    --button-border-radius: var(--size-300);\n\n    .d-btn__icon .d-svg {\n        width: @icon12;\n        height: @icon12;\n    }\n}\n\n//  $$ SMALL\n//  ----------------------------------------------------------------------------\n.d-btn--sm {\n    --button-padding-y: var(--button-padding-y-sm);\n    --button-padding-x: var(--button-padding-x-sm);\n    --button-font-size: var(--fs-100);\n    --button-line-height: var(--button-line-height-sm);\n\n    .d-btn__icon .d-svg {\n        width: @icon14;\n        height: @icon14;\n    }\n}\n\n//  $$  MEDIUM\n//  ----------------\n.d-btn--md {\n    //\n}\n\n//  $$  LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--lg {\n    --button-padding-y: var(--button-padding-y-lg);\n    --button-padding-x: var(--button-padding-x-lg);\n    --button-font-size: var(--fs-300);\n    --button-line-height: var(--button-line-height-lg);\n    --button-border-radius: calc(var(--size-300) * 3);\n\n    .d-btn__icon .d-svg {\n        width: @icon20;\n        height: @icon20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//  ----------------------------------------------------------------------------\n.d-btn--xl {\n    --button-padding-y: var(--button-padding-y-xl);\n    --button-padding-x: var(--button-padding-x-xl);\n    --button-font-size: var(--fs-400);\n    --button-font-weight: var(--fw-normal);\n    --button-line-height: var(--button-line-height-xl);\n    --button-border-radius: var(--size-500);\n\n    .d-btn__icon .d-svg {\n        width: @icon24;\n        height: @icon24;\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON LABEL\n//  ----------------------------------------------------------------------------\n.d-btn__label {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    // background-color: rgba(0,0,0,.5);\n}\n\n//  ============================================================================\n//  $   BUTTON WITH ICON\n//  ----------------------------------------------------------------------------\n.d-btn--vertical {\n    flex-direction: column;\n}\n\n.d-btn__icon {\n    display: flex;\n    // background-color: rgba(0,0,0,.5);\n\n    .d-icon {\n        // outline: 1px solid red;\n        // opacity: 0;\n    }\n}\n\n\n.d-btn__icon--left,\n.d-btn__icon--top {\n    order: -1;\n}\n\n.d-btn__icon--right,\n.d-btn__icon--bottom {\n    order: 1;\n}\n\n.d-btn__icon--left:not(:only-child) {\n    margin-left: calc(var(--space-200) * -1);\n}\n\n.d-btn__icon--right:not(:only-child) {\n    margin-right: calc(var(--space-200) * -1);\n}\n\n//  ============================================================================\n//  $   VISUAL STYLES\n//  ----------------------------------------------------------------------------\n//  $$  CIRCLE BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--circle {\n    --button-padding-x: var(--button-padding-y-md);\n    --button-padding-y: var(--button-padding-y-md);\n    --button-color-text: var(--black-800);\n    --button-border-radius: var(--br-circle);\n\n    &:hover:not([disabled]) {\n        --button-color-text: var(--muted-color-hover);\n        --button-color-background: hsla(var(--black-800-hsl) ~' / ' 3%);\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n        --button-color-text: var(--fc-secondary);\n        --button-color-background: hsla(var(--black-800-hsl) ~' / ' 9%);\n    }\n\n    .d-btn__icon {\n        margin: unset;\n    }\n\n    &.d-btn__label {\n        display: none;\n    }\n\n    &.d-btn--outlined {\n        --button-color-border: var(--black-600);\n    }\n\n    //  Adjust padding based on sizes\n    &.d-btn--xs {\n        --button-padding-y: var(--button-padding-y-xs);\n        --button-padding-x: var(--button-padding-y-xs);\n    }\n\n    &.d-btn--sm {\n        --button-padding-y: var(--button-padding-y-sm);\n        --button-padding-x: var(--button-padding-y-sm);\n    }\n\n    &.d-btn--md {\n        --button-padding-x: var(--button-padding-y-md);\n        --button-padding-y: var(--button-padding-y-md);\n    }\n\n    &.d-btn--lg {\n        --button-padding-x: calc(var(--button-padding-y-lg) + var(--space-100));\n        --button-padding-y: calc(var(--button-padding-y-lg) + var(--space-100));\n    }\n\n    &.d-btn--xl {\n        --button-padding-x: calc(var(--button-padding-y-xl) + var(--space-300));\n        --button-padding-y: calc(var(--button-padding-y-xl) + var(--space-300));\n    }\n}\n\n//  $$  OUTLINED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--outlined {\n    --button-color-border: var(--purple-400);\n}\n\n//  $$  PRIMARY BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--primary {\n    --button-color-text: var(--white);\n    --button-color-background: var(--purple-400);\n\n    &:hover:not([disabled]) {\n      --button-color-text: var(--white);\n      --button-color-background: hsl(var(--purple-400-h), var(--purple-400-s), calc(var(--purple-400-l) - 6%));\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n      --button-color-text: var(--white);\n      --button-color-background: hsl(var(--purple-400-h), var(--purple-400-s), calc(var(--purple-400-l) - 12%));\n    }\n}\n\n//  $$  MUTED BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--muted {\n    --button-color-text: var(--fc-secondary);\n\n    &:hover:not([disabled]) {\n        --button-color-text: var(--fc-secondary);\n        --button-color-background: hsla(var(--black-800-hsl) ~' / ' 5%);\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n        --button-color-text: var(--fc-secondary);\n        --button-color-background: hsla(var(--black-800-hsl) ~' / ' 10%);\n    }\n\n    &.d-btn--outlined {\n        --button-color-border: var(--black-600);\n    }\n}\n\n//  $$  DANGER BUTTON\n//  ----------------------------------------------------------------------------\n.d-btn--danger {\n    --button-color-text: var(--red-300);\n\n    &:hover:not([disabled]) {\n        --button-color-text: hsl(var(--red-300-h), var(--red-300-s), calc(var(--red-300-l) - 8%));\n        --button-color-background: hsla(var(--red-300-hsl) / 0.03);\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n        --button-color-text: hsl(var(--red-300-h), var(--red-300-s), calc(var(--red-300-l) - 8%));\n        --button-color-background: hsla(var(--red-300-hsl) / 0.09);\n    }\n\n    &.d-btn--outlined {\n        --button-color-border: var(--red-300);\n    }\n\n    &.d-btn--primary {\n        --button-color-text: var(--white);\n        --button-color-background: var(--red-300);\n\n        &:hover:not([disabled]) {\n            --button-color-text: var(--white);\n            --button-color-background: hsl(var(--red-300-h), var(--red-300-s), calc(var(--red-300-l) - 4%));\n        }\n\n        &:active:not([disabled]),\n        &.d-btn--active:not([disabled]),\n        &.d-btn--active:active:not([disabled]) {\n            --button-color-text: var(--white);\n            --button-color-background: hsl(var(--red-300-h), var(--red-300-s), calc(var(--red-300-l) - 8%));\n        }\n    }\n}\n\n//  $$  INVERTED BUTTON\n//  ----------------------------------------------------------------------------\n//  --  Base, clear inverted button\n.d-btn--inverted {\n    --button-color-text: var(--white);\n    --button-color-background: transparent;\n\n    &:hover:not([disabled]) {\n        --button-color-text: var(--white);\n        --button-color-background: hsla(var(--white-hsl) ~' / ' 15%);\n    }\n\n    &:active:not([disabled]),\n    &.d-btn--active:not([disabled]),\n    &.d-btn--active:active:not([disabled]) {\n        --button-color-text: var(--white);\n        --button-color-background: hsla(var(--white-hsl) ~' / ' 30%);\n    }\n\n    //  --  OUTLINED\n    &.d-btn--outlined {\n        --button-color-border: var(--white);\n    }\n\n    //  --  PRIMARY\n    &.d-btn--primary {\n        --button-color-text: var(--purple-400);\n        --button-color-background: hsl(var(--purple-100-h), var(--purple-100-s), 100%);\n\n        &:hover:not([disabled]) {\n            --button-color-text: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n            --button-color-background: hsl(var(--purple-100-h), var(--purple-100-s), 94%);\n        }\n\n        &:active:not([disabled]),\n        &.d-btn--active:not([disabled]),\n        &.d-btn--active:active:not([disabled]) {\n            --button-color-text: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n            --button-color-background: hsl(var(--purple-100-h), var(--purple-100-s), 91%);\n        }\n    }\n}\n\n//  $$  ICON ONLY\n//  ----------------------------------------------------------------------------\n.d-btn--icon-only {\n    --button-padding-x: var(--button-padding-y-md);\n    --button-padding-y: var(--button-padding-y-md);\n\n    .d-btn__icon {\n        margin: unset;\n    }\n\n    //  Adjust padding based on sizes\n    &.d-btn--xs {\n        --button-padding-y: var(--button-padding-y-xs);\n        --button-padding-x: var(--button-padding-y-xs);\n    }\n\n    &.d-btn--sm {\n        --button-padding-y: var(--button-padding-y-sm);\n        --button-padding-x: var(--button-padding-y-sm);\n    }\n\n    &.d-btn--md {\n        --button-padding-x: var(--button-padding-y-md);\n        --button-padding-y: var(--button-padding-y-md);\n    }\n\n    &.d-btn--lg {\n        --button-padding-x: calc(var(--button-padding-y-lg) + var(--space-100));\n        --button-padding-y: calc(var(--button-padding-y-lg) + var(--space-100));\n    }\n\n    &.d-btn--xl {\n        --button-padding-x: calc(var(--button-padding-y-xl) + var(--space-300));\n        --button-padding-y: calc(var(--button-padding-y-xl) + var(--space-300));\n    }\n}\n\n//  $$  LOADING STATE\n//  ----------------------------------------------------------------------------\n.d-btn--loading {\n    .d-btn-loading();\n}\n\n//  $$  DISABLED STATE\n//  ----------------------------------------------------------------------------\n.d-btn--disabled {\n    --button-color-text: var(--fc-disabled) !important;\n    --button-color-background: var(--black-300) !important;\n    --button-color-border: transparent !important;\n\n    cursor: not-allowed;\n    transition: none;\n    pointer-events: none;\n}\n\n//  $$  BRAND BUTTONS\n//  ----------------------------------------------------------------------------\n.d-btn--brand {\n    --button-color-text: var(--white);\n    --button-color-background: hsl(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l));\n\n    display: flex;\n\n    &:hover:not([disabled]),\n    &:active:not([disabled]) {\n        --button-color-text: hsla(var(--white-hsl) ~' / ' 90%);\n        --button-color-background: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 2.5%) calc(var(--brand-color-l) - 5%));\n    }\n\n    &:focus-visible {\n        box-shadow: 0 0 0 var(--space-100) var(--white), 0 0 0 0.25em hsla(var(--brand-color-h) var(--brand-color-s) var(--brand-color-l) ~' / ' 90%);\n    }\n\n    &:active:not([disabled]) {\n        --button-color-background: hsl(var(--brand-color-h) calc(var(--brand-color-s) + 5%) calc(var(--brand-color-l) - 10%));\n    }\n\n    .d-btn__icon {\n      align-items: center;\n      justify-content: center;\n      width: 2em;\n      height: 2em;\n      margin-right: var(--space-400);\n      margin-left: calc(var(--space-300) * -1);\n      background-color: var(--white);\n      border-radius: var(--br2);\n    }\n\n    .d-btn__label {\n        flex: 1 auto;\n    }\n}\n\n//  --  GOOGLE\n.d-btn--google {\n    --brand-color-h: 217;\n    --brand-color-s: 89%;\n    --brand-color-l: 61%;\n}\n//  --  OFFICE 365\n.d-btn--o365 {\n    --brand-color-h: 15;\n    --brand-color-s: 99%;\n    --brand-color-l: 46%;\n}\n//  --  LINKEDIN\n.d-btn--linkedin {\n    --brand-color-h: 197;\n    --brand-color-s: 100%;\n    --brand-color-l: 26%;\n}\n\n//  ============================================================================\n//  @   BUTTON LOADER\n//      Changes the button\n//  ----------------------------------------------------------------------------\n//  @@  BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-btn-loading() {\n    //  Disable clicking while the loader is in place\n    pointer-events: none;\n\n    //  Hide anything that was there before\n    .d-btn__icon,\n    .d-btn__label {\n        opacity: 0;\n        transition: opacity 50ms var(--ttf-in-out);\n    }\n\n    //  Show the loading animation\n    &::before {\n        position: absolute;\n        width: var(--size-500);\n        height: var(--size-500);\n        border: var(--size-200) solid currentColor;\n        border-left-color: transparent !important;\n        border-radius: var(--br-circle);\n        animation: d-loading-circle 900ms infinite linear;\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   BUTTON GROUP\n//  ----------------------------------------------------------------------------\n.d-btn-group {\n    display: flex;\n\n    &--start {\n        justify-content: start;\n    }\n\n    &--end {\n        justify-content: end;\n    }\n\n    &--space-between {\n        justify-content: space-between;\n    }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: CARD\n//\n//  These are card classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/card\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • HEADER\n//  • CONTENT\n//  • FOOTER\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ============================================================================\n//  $$  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-card {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  background: var(--bgc-primary);\n  border-radius: var(--size-300);\n  box-shadow: var(--bs-card);\n}\n\n\n//  ============================================================================\n//  $   CARD AREAS\n//  ----------------------------------------------------------------------------\n//  $$  HEADER\n//  ----------------------------------------------------------------------------\n.d-card__header {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: space-between;\n  padding: var(--size-500) var(--size-500) 0;\n}\n\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-card__content {\n  padding: var(--size-500);\n  overflow-y: auto;\n}\n\n//  $$  FOOTER\n//  ----------------------------------------------------------------------------\n.d-card__footer {\n  display: flex;\n  align-items: center;\n  padding: 0 var(--size-500) var(--size-500);\n}\n","//\n//  DIALTONE\n//  COMPONENTS: CHIP\n//\n//  These are chip classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/chip\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • INTERACTIVITY\n//  • SIZES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-chip {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n}\n\n.d-chip__label {\n  //  Component CSS Vars\n  --chip-color-text: var(--fc-primary);\n  --chip-color-background: var(--black-200);\n  --chip-border-radius: var(--br-pill);\n\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n  padding: var(--space-300) var(--space-400);\n  color: var(--chip-color-text);\n  font-size: var(--fs-200);\n  font-family: inherit;\n  line-height: var(--lh4);\n  background-color: var(--chip-color-background);\n  border: none;\n  border-radius: var(--chip-border-radius);\n  transition-timing-function: var(--ttf-in-out);\n  transition-duration: 75ms;\n  transition-property: background-color;\n\n  // Reserves space within the chip for the close button, since the close button is\n  // not nested within the chip. Only apply if close button exists (more than one child).\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: calc(var(--size-500) + var(--space-200));\n    height: calc(var(--size-500) + var(--space-200));\n    content: '';\n  }\n\n  // These properties are only set if d-chip is interactive (ex: a button).\n  &:is(a),\n  &:is(button),\n  &:is([role='button']),\n  &:is([role='link']) {\n    text-decoration: none;\n    cursor: pointer;\n\n    &:hover {\n      --chip-color-background: var(--black-300);\n\n      text-decoration: none;\n    }\n\n    &:active {\n      --chip-color-background: var(--black-400);\n    }\n\n    &:focus-visible {\n      outline: none;\n      box-shadow: var(--bs-focus-ring);\n    }\n  }\n\n  .d-avatar {\n    --avatar-size-shape: calc(var(--size-600) - var(--space-400));\n\n    margin: calc(var(--space-200) * -1) var(--space-300) calc(var(--space-200) * -1) calc(calc(var(--space-300) + var(--space-200)) * -1);\n  }\n\n  .d-svg {\n    width: @icon18;\n    height: @icon18;\n  }\n}\n\n.d-chip__close {\n  .d-btn();\n  .d-btn--circle();\n\n  position: absolute;\n  right: var(--space-200);\n  padding: calc(var(--space-200) + var(--space-100));\n  border-width: 0;\n\n  &::before {\n    position: absolute;\n    width: 2.6rem; // magic number\n    height: 2.8rem; // magic number\n    content: '';\n  }\n\n  &:hover {\n    --button--bgc: hsla(var(--black-800-hsl) ~' / ' 15%);\n  }\n\n  &:active {\n    --button--bgc: hsla(var(--black-800-hsl) ~' / ' 25%);\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon18;\n    height: @icon18;\n  }\n}\n\n.d-chip__icon {\n  padding-right: var(--space-300);\n  line-height: 0;\n}\n\n.d-chip__label--active {\n  background-color: var(--black-400);\n}\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n.d-chip__label--xs {\n  padding: var(--space-200) calc(var(--space-200) + var(--space-300));\n  font-size: var(--fs-100);\n\n  .d-svg {\n    width: @icon14;\n    height: @icon14;\n  }\n\n  // reserves space within the chip for the close button, since the close button is\n  // not nested within the chip.\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: calc(var(--size-400) + var(--size-300));\n    height: calc(var(--size-400) + var(--size-300));\n    content: '';\n  }\n\n  .d-avatar {\n    --avatar-size-shape: var(--size-500);\n\n    margin-right: var(--space-300);\n    margin-left: calc(var(--space-300) * -1);\n  }\n}\n\n.d-chip__close--xs {\n  padding: var(--space-100);\n\n  // Clickable area for the close button.\n  &::before {\n    width: calc(var(--size-600) - var(--size-400)); // 24\n    height: calc(var(--size-600) - var(--size-400)); // 24\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon14;\n    height: @icon14;\n  }\n}\n\n//  $$ SMALL\n//  ----------------------------------------------------------------------------\n.d-chip__label--sm {\n  padding: var(--space-200) var(--space-400);\n  font-size: var(--fs-200);\n\n  .d-svg {\n    width: @icon16;\n    height: @icon16;\n  }\n\n  // reserves space within the chip for the close button, since the close button is\n  // not nested within the chip.\n  &:not(:only-child)::after {\n    flex-shrink: 0;\n    width: calc(var(--size-500) - var(--size-200)); // 14\n    height: calc(var(--size-500) - var(--size-200)); // 14\n    content: '';\n  }\n\n  .d-avatar {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-300)); // 20\n\n    margin-right: var(--space-300);\n  }\n}\n\n.d-chip__close--sm {\n  padding: var(--space-200);\n\n  &::before {\n    width: calc(var(--size-600) - var(--space-400)); // 24\n    height: calc(var(--size-600) - var(--space-400)); // 24\n  }\n\n  .d-btn__icon .d-svg {\n    width: @icon16;\n    height: @icon16;\n  }\n}\n","//@import (reference) '../variables/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: FORMS\n//\n//  These are form classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/forms\n//\n//  TABLE OF CONTENTS\n//  • FIELDSETS\n//  • LABELS\n//    - Sizes\n//  • DESCRIPTIONS\n//    - Sizes\n//  • VALIDATION MESSAGES\n//    - Validation States\n//\n//  ============================================================================\n//  $  FIELDSETS\n//  ----------------------------------------------------------------------------\nfieldset {\n  min-width: 0;\n  margin: 0;\n  padding: 0;\n  border: none;\n}\n\n\n//  ============================================================================\n//  $  LABELS\n//  ----------------------------------------------------------------------------\n.d-label {\n  display: flex;\n  flex: 1 0%;\n  align-items: baseline;\n  justify-content: space-between;\n  box-sizing: border-box;\n  margin-bottom: var(--space-300);\n  color: var(--fc-secondary);\n  font-weight: var(--fw-semibold);\n  font-size: var(--fs-200);\n  font-family: inherit;\n  line-height: var(--lh-300);\n  word-break: break-word;\n  overflow-wrap: break-word;\n\n  legend & { cursor: default; }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-label--xs {\n  font-size: var(--fs-100);\n}\n\n.d-label--sm {\n  font-size: var(--fs-200);\n}\n\n.d-label--lg {\n  font-size: var(--fs-200);\n}\n\n.d-label--xl {\n  font-size: var(--fs-300);\n}\n\n//  ============================================================================\n//  $ DESCRIPTIONS\n//  ----------------------------------------------------------------------------\n.d-description {\n  display: flex;\n  box-sizing: border-box;\n  color: var(--fc-tertiary);\n  font-size: var(--fs-100);\n  font-family: inherit;\n  line-height: var(--lh-400);\n  fill: currentColor;\n}\n\n.d-label + .d-description {\n  margin-top: calc(var(--space-300) * -1);\n  margin-bottom: var(--space-300);\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-description--lg {\n  font-size: var(--fs-200);\n}\n\n.d-description--xl {\n  font-size: var(--fs-200);\n}\n\n//  ============================================================================\n//  $   VALIDATION MESSAGES\n//  ----------------------------------------------------------------------------\n.d-validation-message {\n  --validation-color-text: var(--fc-tertiary);\n\n  display: flex;\n  gap: var(--space-300);\n  align-items: flex-start;\n  margin-top: var(--space-400);\n  color: var(--validation-color-text);\n  font-weight: var(--fw-medium);\n  font-size: var(--fs-100);\n  font-family: inherit;\n  line-height: var(--lh-300);\n\n  // Icon Slot\n  &::before {\n    display: block;\n    width: calc(var(--size-500) + var(--size-200)); // 16\n    height: var(--size-500); // 16\n    content: '';\n  }\n}\n\n//  $$   VALIDATION STATES\n//  ----------------------------------------------------------------------------\n\n.d-validation-message--warning {\n  --validation-color-text: var(--fc-warning);\n\n  &::before {\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--error {\n  --validation-color-text: var(--fc-critical);\n\n  &::before {\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n  }\n}\n\n.d-validation-message--success {\n  --validation-color-text: var(--fc-success);\n\n  &::before {\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n  }\n}\n","//@import (reference) '../variables/icons.less';\n//@import (reference) '../variables/sizes.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: INPUTS\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//  • VARIABLES\n//  • INPUTS & TEXTAREAS\n//    - Base Style\n//    - Wrapper\n//    - Validation States\n//    - Sizes\n//  • INPUT ICONS\n//\n\n//  $  INPUTS\n//  ----------------------------------------------------------------------------\n.d-input,\n.d-input--md,\n.d-textarea,\n.d-textarea--md,\n.d-input__wrapper--md,\n.d-input__wrapper {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input-color-border: hsla(var(--black-900-hsl) / 0.09);\n    --input-color-background: hsla(var(--black-900-hsl) / 0.03);\n    --input-color-background-disabled: hsla(var(--black-900-hsl) / 0.12);\n    --input-color-text: var(--fc-secondary);\n    --input-border-width: calc(var(--size-100) + calc(var(--size-100) / 2)); // 1.5\n    --input-border-radius: var(--size-400);\n    --input-padding-y: calc(var(--space-400) - var(--input-border-width));\n    --input-padding-x: calc((var(--space-500) - var(--space-300)) - var(--input-border-width));\n    --input-font-size: var(--fs-200);\n    --input-line-height: var(--lh-200);\n\n    position: relative;\n    display: inline-flex;\n    flex: 1 0%;\n    box-sizing: border-box;\n    width: 100%;\n    min-width: 0;\n    padding: var(--input-padding-y) var(--input-padding-x);\n    color: var(--input-color-text);\n    font-weight: inherit;\n    font-size: var(--input-font-size);\n    font-family: inherit;\n    line-height: var(--input-line-height);\n    background-color: var(--input-color-background);\n    border: var(--input-border-width) solid var(--input-color-border);\n    border-radius: var(--input-border-radius);\n    outline: none;\n    box-shadow: none;\n    transition-timing-function: var(--ttf-in-out);\n    transition-duration: 100ms;\n    transition-property: box-shadow, background-color;\n    appearance: none;\n\n    //  --  Placeholder copy\n    &::placeholder {\n        color: var(--fc-placeholder);\n    }\n    //  --  Remove input EDGE additions\n    &::-ms-clear {\n        display: none;\n    }\n    //  --  FOCUS STATE\n    &:focus,\n    &:focus-within {\n        --input-color-background: hsla(var(--black-900-hsl) / 0.01);\n        --input-color-border: var(--color-focus-ring) !important;\n\n        box-shadow: 0 0 0 var(--size-100) var(--color-focus-ring) inset;\n    }\n    //  --  DISABLED / READ-ONLY\n    &[disabled],\n    &[read-only] {\n        --input-color-border: transparent !important;\n        --input-color-background: var(--input-color-background-disabled);\n        --input-color-text: var(--fc-disabled);\n\n        &:focus,\n        &:focus-within {\n            box-shadow: none !important;\n        }\n\n        //  --  Placeholder copy\n        &::placeholder {\n            color: var(--fc-placeholder);\n        }\n    }\n\n    //  --  DISABLED\n    &[disabled] {\n        cursor: not-allowed;\n    }\n\n    //  --  BROWSER-SPECIFIC\n    &::-moz-focus-inner {\n        outline: none !important;\n    }\n\n    &:-moz-focusring {\n        color: transparent;\n        text-shadow: 0 0 0 var(--black-900);\n    }\n\n    &::-ms-expand {\n        display: none;\n    }\n}\n\n//  $$  INPUT WRAPPER\n//  ----------------------------------------------------------------------------\n.d-input__wrapper {\n    padding: 0;\n\n    .d-input-icon.d-input-icon--right {\n        margin-right: var(--space-400);\n    }\n\n    .d-input-icon.d-input-icon--left {\n        margin-left: var(--space-400);\n    }\n\n    .d-input,\n    .d-textarea {\n        flex: 1;\n        background-color: transparent;\n        border: none;\n\n        &:focus {\n            border: none;\n            outline: 0;\n            box-shadow: none !important;\n        }\n\n        &.d-input-icon--right {\n            padding-right: calc(var(--space-300) + var(--space-200));\n        }\n\n        &.d-input-icon--left {\n            padding-left: calc(var(--space-300) + var(--space-200));\n        }\n    }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-input.d-input--xs {\n    #d-internal__input-and-select-xs();\n}\n\n.d-input.d-input--sm {\n    #d-internal__input-and-select-sm();\n}\n\n.d-input.d-input--lg {\n    #d-internal__input-and-select-lg();\n}\n\n.d-input.d-input--xl {\n    #d-internal__input-and-select-xl();\n}\n\n\n//  ============================================================================\n//  $  TEXTAREAS\n//  ----------------------------------------------------------------------------\n.d-textarea {\n    min-height: calc(var(--size-300) * 20); // 80\n    vertical-align: top;\n    border-bottom-right-radius: var(--size-300);\n    resize: vertical;\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-textarea--xs {\n    #d-internal__input-and-select-xs();\n\n    min-height: calc(var(--size-300) * 10); // 40\n}\n\n.d-textarea--sm {\n    #d-internal__input-and-select-sm();\n\n    min-height: calc(var(--size-300) * 12); // 48\n}\n\n.d-textarea--lg {\n    #d-internal__input-and-select-lg();\n\n    min-height: calc(var(--size-300) * 23); // 92\n}\n\n.d-textarea--xl {\n    #d-internal__input-and-select-xl();\n\n    min-height: calc(var(--size-300) * 25); // 100\n}\n\n//  $$  VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-input--warning,\n.d-textarea--warning {\n    --input-color-border: var(--gold-400) !important;\n}\n\n.d-input--error,\n.d-textarea--error {\n    --input-color-border: var(--red-300) !important;\n}\n\n.d-input--success,\n.d-textarea--success {\n    --input-color-border: var(--green-400) !important;\n}\n\n//  ============================================================================\n//  $  INPUT ICONS\n//  ----------------------------------------------------------------------------\n.d-input-icon {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --input-icon-size: var(--size-500);\n    --input-icon-container-height: var(--size-600);\n\n    z-index: var(--zi-base1);\n    display: inline-flex;\n    align-items: center;\n    height: var(--input-icon-container-height);\n    margin: 0;\n\n    svg {\n        // Backwards-compatible to DT6 icons\n        width: var(--input-icon-size);\n        height: var(--input-icon-size);\n    }\n}\n\n\n\n//  $$ SIZES\n//  ----------------------------------------------------------------------------\n.d-input-icon.d-input--xs {\n    --input-icon-container-height: calc(var(--size-600) - var(--size-300));\n}\n\n.d-input-icon--xs {\n    // Backwards-compatible to DT6 icons\n    --input-icon-size: @icon12;\n}\n\n.d-input-icon.d-input--sm {\n    --input-icon-container-height: var(--size-600);\n}\n\n.d-input-icon--sm {\n    // Backwards-compatible to DT6 icons\n    --input-icon-size: @icon14;\n}\n\n.d-input-icon.d-input--lg {\n    --input-icon-container-height: calc(var(--size-300) * 10);\n}\n\n.d-input-icon--lg {\n    // Backwards-compatible to DT6 icons\n    --input-icon-size: @icon20;\n}\n\n.d-input-icon.d-input--xl {\n    --input-icon-container-height: calc(var(--size-300) * 14);\n}\n\n.d-input-icon--xl {\n    // Backwards-compatible to DT6 icons\n    --input-icon-size: @icon24;\n}\n","//\n//  DIALTONE\n//  TOKENS: SIZES\n//\n//  These are shared sizes for buttons, inputs, select menus (native),\n//  and textareas.\n//  ============================================================================\n//  $   SHARED INPUT & BUTTON SIZES\n//  ============================================================================\n//  $$  EXTRA SMALL\n//  ----------------------------------------------------------------------------\n#d-internal__input-and-select-xs() {\n    --input-padding-y: calc(calc(var(--space-400) - var(--space-100)) - var(--input-border-width));\n    --input-padding-x: calc(var(--space-400) - var(--input-border-width));\n    --input-font-size: var(--fs-100);\n    --input-border-radius: var(--size-300);\n\n    .d-btn__icon {\n        width: @icon-size14;\n        height: @icon-size14;\n    }\n}\n\n//  $$  SMALL\n//  ----------------------------------------------------------------------------\n#d-internal__input-and-select-sm() {\n    --input-padding-y: calc(var(--space-400) - var(--input-border-width));\n    --input-padding-x: calc((var(--space-500) - var(--space-300)) - var(--input-border-width));\n    --input-font-size: var(--fs-100);\n\n    .d-btn__icon {\n        width: @icon-size16;\n        height: @icon-size16;\n    }\n}\n\n//  $$  LARGE\n//  ----------------------------------------------------------------------------\n#d-internal__input-and-select-lg() {\n    --input-padding-y: calc((var(--space-400) + var(--space-200)) - var(--input-border-width));\n    --input-padding-x: calc(var(--space-500) - var(--input-border-width));\n    --input-font-size: var(--fs-300);\n    --input-border-radius: calc(var(--size-300) * 3);\n\n    .d-btn__icon {\n        width: @icon-size20;\n        height: @icon-size20;\n    }\n}\n\n//  $$  EXTRA LARGE\n//  ----------------------------------------------------------------------------\n#d-internal__input-and-select-xl() {\n    --input-padding-y: calc((var(--space-500) - var(--space-300)) - var(--input-border-width));\n    --input-padding-x: calc(var(--space-500) - var(--input-border-width));\n    --input-font-size: var(--fs-400);\n    --input-border-radius: var(--size-500);\n\n    .d-btn__icon {\n        width: @icon-size24;\n        height: @icon-size24;\n    }\n}\n\n#d-internals #spacing-classes(0.8rem, 'size');\n\n#d-internals #spacing-classes(0.8rem, 'size');\n#d-internals #spacing-classes(1.6rem, 'size', '-16');\n#d-internals #spacing-classes(3.2rem, 'size', '-32');\n#d-internals #spacing-classes(6.4rem, 'size', '-64');\n#d-internals #spacing-classes(12.8rem, 'size', '-128');\n","//\n//  DIALTONE\n//  COMPONENTS: LINKS\n//\n//  These are link classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/links\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-link {\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    color: var(--purple-400);\n    font: inherit;\n    text-decoration: underline;\n    background-color: transparent;\n    border: 0;\n    transition-timing-function: var(--ttf-out-quint);\n    transition-duration: var(--td300);\n    transition-property: background-color, border, box-shadow;\n\n    //  Reset button appearance\n    -webkit-appearance: none;\n       -moz-appearance: none;\n            appearance: none;\n    fill: currentColor;\n\n    &:hover {\n        color: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n        text-decoration: none;\n        cursor: pointer;\n    }\n\n    &:focus-visible {\n        outline: none;\n        box-shadow: var(--bs-focus-ring);\n    }\n}\n\n\n//  ============================================================================\n//  $   ALTERNATE STYLES\n//  ============================================================================\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-link--warning {\n    color: var(--fc-warning);\n\n    &:hover {\n        color: var(--fc-warning-hover);\n    }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-link--danger {\n    color: var(--fc-critical);\n\n    &:hover {\n        color: hsl(var(--red-300-h), var(--red-300-s), calc(var(--red-300-l) - 8%));\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-link--success {\n    color: var(--fc-success);\n\n    &:hover {\n        color: var(--green-500);\n    }\n}\n\n//  $$  MUTED\n//  ----------------------------------------------------------------------------\n.d-link--muted {\n    color: var(--fc-secondary);\n\n    &:hover {\n        color: var(--fc-primary);\n    }\n}\n\n//  $$  DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link[disabled],\n.d-link--disabled {\n    color: var(--fc-disabled);\n\n    &:hover {\n        color: var(--fc-disabled);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n\n//  $$  INVERTED\n//  ----------------------------------------------------------------------------\n.d-link--inverted {\n    color: var(--fc-primary-inverted);\n\n    &:hover {\n        color: var(--fc-secondary-inverted);\n    }\n}\n\n//  $$  INVERTED DISABLED\n//  ----------------------------------------------------------------------------\n// We don't expose these in the documentation because they're meant to be consumed\n// by buttons using .d-link. Links with a disabled attribute are not valid mark-up.\n.d-link--inverted-disabled {\n    color: var(--black-500);\n\n    &:hover {\n        color: var(--black-500);\n        text-decoration: underline;\n        cursor: not-allowed;\n    }\n\n    &:focus {\n        box-shadow: none;\n    }\n}\n","//\n//  DIALTONE 5 SHIM\n//  COMPONENTS: LIST GROUPS\n//\n//  These are the list group definitions, which are used by classes or can be\n//  used within Vue components. For further documentation of these values,\n//  please visit https://dialpad.design/components/list-groups\n//\n//  ============================================================================\n.d-list-group,\n.d-list-group__item {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n}\n\n.d-list-group--header,\n.d-list-group--link {\n    display: block;\n    padding: var(--size-300) calc(var(--size-300) * 6); // 4 24\n}\n\n.d-list-group--header {\n    font-weight: var(--fw-bold);\n    font-size: var(--fs-100);\n    text-transform: uppercase;\n}\n\n.d-list-group--link {\n    color: var(--black-800);\n    font-size: var(--fs-200);\n    text-decoration: none;\n\n    &:hover {\n        color: var(--black-900);\n        background-color: hsla(var(--purple-400-hsl) ~' / ' 85%);\n    }\n\n    &-selected {\n        color: var(--white);\n        background-color: var(--purple-400);\n\n        &:hover {\n            color: var(--white);\n            background-color: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) + 10%));\n        }\n    }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: MODALS\n//\n//  These are modal classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/modals\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLES\n//    - Wrapper\n//    - Dialog\n//    - Make them appear\n//  • MODAL BACKGROUND\n//  • HEADER\n//  • CONTENT\n//  • FOOTER\n//  • TRANSITIONS\n//\n//  ============================================================================\n//  $   BASE STYLES\n//  ============================================================================\n//  $$  WRAPPER\n//  ----------------------------------------------------------------------------\n.d-modal {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --modal-backdrop-color-background: hsla(var(--bgc-contrast-hsl) / 0.7);\n    --modal-dialog-padding: var(--space-600);\n    --modal-dialog-color-background: var(--bgc-primary);\n    --modal-dialog-color-text: var(--fc-tertiary);\n    --modal-header-color-text: var(--fc-secondary);\n    --modal-dialog-color-shadow: hsla(var(--black-900-hsl) / 0.1);\n    --modal-dialog-shadow: 0 0 0 var(--size-100) var(--modal-dialog-color-shadow), var(--bs-card);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: var(--zi-hide);\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    padding: var(--space-600); // 32\n    background-color: var(--modal-backdrop-color-background);\n    backface-visibility: hidden;\n    visibility: hidden;\n    opacity: 0;\n    will-change: visibility, z-index, opacity;\n}\n\n.d-modal--transparent {\n  --modal-backdrop-color-background: var(--d-bgc-transparent);\n}\n\n//  $$  MODAL DIALOG\n//  ----------------------------------------------------------------------------\n.d-modal__dialog {\n  position: relative;\n  z-index: var(--zi-hide);\n  display: flex;\n  flex-direction: column;\n  gap: var(--space-500); //\n  width: 100%;\n  max-width: calc(var(--size-300) * 157); // 628\n  max-height: 100%;\n  padding: var(--modal-dialog-padding); // 32\n  overflow-y: auto;\n  color: var(--modal-dialog-color-text);\n  font-size: var(--fs-200);\n  line-height: var(--lh-400);\n  background-color: var(--modal-dialog-color-background);\n  border-radius: var(--size-500);\n  box-shadow: var(--modal-dialog-shadow);\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75);\n  backface-visibility: hidden;\n  visibility: hidden;\n  opacity: 0;\n  will-change: visibility, z-index, opacity, transform;\n}\n\n//  $$   MAKE THEM APPEAR\n//  ----------------------------------------------------------------------------\n.d-modal[aria-hidden='false'],\n.d-modal[aria-hidden='false'] .d-modal__dialog,\n.d-modal--transparent[aria-hidden='false'],\n.d-modal--transparent[aria-hidden='false'] .d-modal__dialog {\n  z-index: var(--zi-modal);\n  transform: translate3d(0, 0, 0) scale3d(1, 1, 1);\n  visibility: visible;\n  opacity: 1;\n}\n\n\n//  ============================================================================\n//  $   MODAL AREAS\n//  ----------------------------------------------------------------------------\n.d-modal__footer {\n  --modal-footer-padding: calc(var(--space-300) * 6); // 24\n\n  display: flex;\n  flex-direction: row-reverse;\n  gap: var(--space-400);\n  align-items: center;\n}\n\n\n//  ============================================================================\n//  $   TYPE STYLES\n//  ============================================================================\n//  $$  HEADER\n//  ----------------------------------------------------------------------------\n.d-modal__header {\n  --modal-header-padding: calc(var(--space-300) * 6); // 24\n\n  margin: 0 !important;\n  color: var(--modal-header-color-text);\n  font-weight: var(--fw-medium);\n  font-size: var(--fs-400);\n  line-height: var(--lh-100);\n}\n\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-modal__content {\n  max-width: 75ch;\n}\n\n\n//  ============================================================================\n//  $   BUTTONS\n//  ----------------------------------------------------------------------------\n.d-modal__close {\n  position: absolute;\n  top: var(--space-500); // 16\n  right: var(--space-500); // 16\n  margin: 0 !important;\n}\n\n\n//  ============================================================================\n//  $   BANNERS\n//  ----------------------------------------------------------------------------\n.d-modal__banner {\n  --modal-banner-padding-y: var(--space-500);\n  --modal-banner-padding-x: var(--space-600);\n  --modal-banner-color-background: var(--bgc-warning);\n\n  position: relative;\n  box-sizing: border-box;\n  width: 100%;\n  max-width: calc(var(--size-300) * 157); // 628\n  padding: var(--modal-banner-padding-y) var(--modal-banner-padding-x); // 16 32\n  color: var(--fc-primary);\n  font-size: var(--fs-200);\n  line-height: var(--lh-300);\n  background-color: var(--modal-banner-color-background);\n  border-radius: var(--size-500) var(--size-500) 0 0;\n  box-shadow: var(--modal-dialog-shadow);\n\n  &::before {\n    // 🤦 don't ask. or do, i'm not even sorry.\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    z-index: var(--zi-modal-element);\n    height: var(--modal-banner-padding-y);\n    background-color: var(--modal-banner-color-background);\n    content: '';\n  }\n\n  &:not(.d-d-none) + .d-modal__dialog {\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n  }\n}\n\n\n//  ============================================================================\n//  $   MODIFYING STYLES\n//  ============================================================================\n//  $$  FULL WIDTH\n//  ----------------------------------------------------------------------------\n.d-modal--full {\n  --modal-dialog-padding: 0;\n\n  padding: 0;\n  overflow: hidden scroll;\n  transform: unset !important;\n\n  .d-modal__dialog {\n    display: flex;\n    flex-direction: column;\n    max-width: unset;\n    height: 100%;\n    border-radius: 0;\n    transform: unset !important;\n  }\n\n  .d-modal__header {\n    padding: var(--space-600) var(--space-600) 0; // 32\n  }\n\n  .d-modal__content {\n    padding-right: 0;\n    padding-left: var(--space-600); // 32\n  }\n\n  .d-modal__footer {\n    margin-top: auto !important;\n    padding: 0 calc(var(--space-300) * 6) var(--space-600) var(--space-600); // 0 24 32 32\n  }\n\n  .d-modal__banner {\n    max-width: unset;\n    padding: calc(var(--space-300) * 3) var(--space-600); // 12 32\n    border-radius: 0;\n  }\n}\n\n// $$   SCROLLABLE\n//  ----------------------------------------------------------------------------\n.d-modal__dialog--scrollable {\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n\n  .d-modal__content {\n    overflow-y: auto;\n  }\n}\n\n//  $$  DANGER\n//  ----------------------------------------------------------------------------\n.d-modal--danger {\n  --modal-header-color-text: var(--fc-secondary);\n}\n\n\n//  ============================================================================\n//  $   TRANSITIONS\n//  ----------------------------------------------------------------------------\n.d-modal--animate {\n  z-index: var(--zi-hide) !important;\n  backface-visibility: hidden !important;\n  visibility: hidden !important;\n  opacity: 0 !important;\n}\n\n.d-modal__dialog--animate {\n  .d-modal--animate();\n\n  transform: translate3d(0, 30%, 0) scale3d(0.75, 0.75, 0.75) !important;\n}\n\n.d-modal--animate-in,\n .d-modal__dialog--animate-in {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 0s,\n    visibility 0s 0s,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal--animate-out {\n  transition:\n    opacity 100ms var(--ttf-in-out) 10ms,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 10ms;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n\n.d-modal__dialog--animate-out {\n  transition:\n    opacity 200ms var(--ttf-in-out) 0s,\n    z-index 0s 200ms,\n    visibility 0s 200ms,\n    transform 100ms var(--ttf-in-out) 0s;\n\n  &.d-modal--full {\n    transition:\n      opacity 200ms var(--ttf-in-out) 0s,\n      z-index 0s 200ms,\n      visibility 0s 200ms;\n  }\n\n  @media (prefers-reduced-motion) {\n    transition: none !important;\n  }\n}\n","//@import (reference) '../utilities/spacing.less';\n//@import (reference) '../utilities/effects.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOAST\n//\n//  These are the styles for toast notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/toasts\n\n//  ============================================================================\n//  @   TOAST CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast-wrapper {\n  position: absolute;\n  top: var(--size-600); // 32\n  left: 50%;\n  display: flex;\n  flex-direction: column;\n  gap: var(--space-500);\n  transform: translateX(-50%);\n}\n\n//  ============================================================================\n//  @   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toast {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --toast-color-background: var(--bgc-secondary);\n    --toast-color-text: var(--fc-primary);\n    --notice-color-icon: var(--toast-color-text);\n    --toast-color-shadow: hsla(var(--black-900-hsl) ~' / ' 15%);\n    --toast-padding: var(--space-500);\n    --toast-border-radius: var(--size-400);\n    --toast-box-shadow: 0 0 0 var(--size-100) var(--toast-color-shadow) inset, var(--bs-md);\n    --toast-font-size: var(--fs-200);\n    --toast-line-height: var(--lh-300);\n\n    z-index: var(--zi-notification);\n    box-sizing: border-box;\n    width: 100%;\n    max-width: calc(var(--size-300) * 157); // 628\n    padding: var(--toast-padding);\n    color: var(--toast-color-text);\n    font-size: var(--toast-font-size);\n    line-height: var(--toast-line-height);\n    word-break: normal;\n    background-color: var(--toast-color-background);\n    border-radius: var(--toast-border-radius);\n    box-shadow: var(--toast-box-shadow);\n\n    //  If there's a link in a toast, break the url so it wraps\n    > a {\n        word-break: break-all;\n    }\n\n    // If you want to hide and reveal the toast\n    &[aria-hidden='true'] {\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-toast__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n\n    .d-notice__content {\n        margin-right: var(--space-500); // 16\n    }\n\n    .d-notice__actions {\n        margin-left: var(--space-600); // 32\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-toast.d-toast--important {\n    --toast-color-background: var(--bgc-contrast);\n    --toast-color-text: var(--black-100);\n    --notice-color-icon: var(--toast-color-text);\n  }\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-toast--error {\n    --toast-color-background: var(--bgc-critical);\n\n    &.d-toast--important {\n      --toast-color-background: var(--red-400);\n      --toast-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-toast--info {\n    --toast-color-background: var(--bgc-info);\n\n    &.d-toast--important {\n        --toast-color-background: var(--blue-400);\n        --toast-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-toast--success {\n    --toast-color-background: var(--bgc-success);\n\n    &.d-toast--important {\n        --toast-color-background: var(--green-400);\n        --toast-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-toast--warning {\n    --toast-color-background: var(--bgc-warning);\n\n    &.d-toast--important {\n        --toast-color-background: var(--gold-200);\n        --toast-color-text: var(--fc-primary);\n    }\n}\n\n//  ============================================================================\n//  $   UC Toast Shims\n//  ============================================================================\n//  $$  CHAT\n//  ----------------------------------------------------------------------------\n.d-toast--chat {\n  .d-notice__icon,\n  .d-toast__meta {\n    color: var(--fc-tertiary);\n  }\n\n  .d-toast__meta {\n    font-size: var(--fs-100);\n  }\n\n  &.d-toast--important {\n    .d-notice__icon,\n    .d-toast__meta {\n      color: var(--fc-secondary-inverted);\n    }\n  }\n}\n\n//  $$  ORGANIZER\n//  ----------------------------------------------------------------------------\n.d-toast--organizer {\n  //\n}\n\n//  $$  VIEWING\n//  ----------------------------------------------------------------------------\n// TODO: resolve\n.d-toast--viewing {\n //\n}\n\n.d-toast--organizer,\n.d-toast--viewing {\n  --toast-border-radius: var(--size-200);\n\n  border-top: var(--size-300) solid var(--magenta-300);\n\n  .d-notice__icon {\n    color: var(--magenta-300);\n  }\n}\n\n.d-toast--chat,\n.d-toast--organizer,\n.d-toast--viewing {\n  --toast-color-background: var(--bgc-secondary);\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: NOTICES\n//\n//  These are the styles for notices, which are used by alert users to\n//  information they might need. For further documentation of these styles,\n//  please visit https://dialpad.design/components/notices\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n\n.d-notice {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --notice-color-background: var(--bgc-secondary);\n    --notice-color-text: var(--fc-primary);\n    --notice-color-icon: var(--notice-color-text);\n    --notice-color-shadow: hsla(var(--black-900-hsl) / 0.06);\n    --notice-padding: var(--space-500);\n    --notice-font-size: var(--fs-200);\n    --notice-line-height: var(--lh-300);\n    --notice-border-radius: var(--size-400);\n    --notice-box-shadow: 0 0 0 var(--size-100) var(--notice-color-shadow) inset;\n\n    //  Base Styles\n    //  ------------------------------------------------------------------------\n    display: flex;\n    align-items: center;\n    box-sizing: border-box;\n    width: 100%;\n    max-width: calc(var(--size-300) * 157); // 628\n    padding: var(--notice-padding);\n    color: var(--notice-color-text);\n    font-size: var(--notice-font-size);\n    line-height: var(--notice-line-height);\n    background-color: var(--notice-color-background);\n    border-radius: var(--notice-border-radius);\n    box-shadow: var(--notice-box-shadow);\n\n    .d-toast & {\n        max-width: calc(var(--size-300) * 116); // 464\n        box-shadow: var(--bs-sm);\n        pointer-events: all;\n    }\n}\n\n//  ============================================================================\n//  $   NOTICE AREAS\n//  ============================================================================\n//  $$  CONTENT\n//  ----------------------------------------------------------------------------\n.d-notice__content {\n    display: flex;\n    flex: 1 auto;\n    flex-direction: column;\n    margin-right: var(--space-500);\n}\n\n//  $$  ACTIONS\n//  ----------------------------------------------------------------------------\n.d-notice__actions {\n    display: flex;\n    flex: 0 auto;\n    gap: var(--space-400);\n    align-items: center;\n}\n\n//  $$  ICON\n//  ----------------------------------------------------------------------------\n.d-notice__icon {\n    display: flex;\n    flex: 0 auto;\n    margin-right: calc(var(--space-300) * 3);\n    color: var(--notice-color-icon);\n\n    .d-banner & {\n        margin-right: var(--space-400);\n    }\n}\n\n//  ============================================================================\n//  $   COPY STYLES\n//  ============================================================================\n.d-notice__title {\n    font-weight: var(--fw-bold);\n    font-size: inherit;\n}\n\n.d-notice__message {\n    .d-notice__title:not([hidden]) + & {\n        font-size: var(--fs-100);\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-notice.d-notice--important {\n    --notice-color-background: var(--bgc-contrast);\n    --notice-color-text: var(--black-100);\n    --notice-color-shadow: transparent;\n    --notice-color-icon: var(--fc-primary-inverted);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-notice--error,\n.d-banner--error,\n.d-toast--error {\n    --notice-color-background: var(--bgc-critical);\n    --notice-color-icon: var(--red-400);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice-color-background: var(--red-400);\n        --notice-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-notice--info,\n.d-banner--info,\n.d-toast--info {\n    --notice-color-background: var(--bgc-info);\n    --notice-color-icon: var(--blue-500);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice-color-background: var(--blue-400);\n        --notice-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-notice--success,\n.d-banner--success,\n.d-toast--success {\n    --notice-color-background: var(--bgc-success);\n    --notice-color-icon: var(--green-400);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice-color-background: var(--green-400);\n        --notice-color-text: var(--fc-primary-inverted);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-notice--warning,\n.d-banner--warning,\n.d-toast--warning {\n    --notice-color-background: var(--bgc-warning);\n    --notice-color-icon: var(--gold-400);\n\n    &.d-notice--important,\n    &.d-banner--important,\n    &.d-toast--important {\n        --notice-color-background: var(--gold-200);\n        --notice-color-text: var(--fc-primary);\n        --notice-color-icon: var(--notice-color-text);\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: POPOVER\n//\n//  These are popover classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/popover\n//\n//  TABLE OF CONTENTS\n//  • POPOVER\n//  • CSS CUSTOM PROPERTIES\n//  • POPOVER DIALOG\n//    - Base dialog style\n//    - Content\n//    - Header / Footer\n\n//  $  CSS CUSTOM PROPERTIES\n//  ----------------------------------------------------------------------------\nbody {\n  --popover-color-background: var(--black-100);\n  --popover-border-width: var(--size-100); // 8\n  --popover-border-radius: var(--size-400);\n  --popover-color-shadow: hsla(var(--black-900-hsl) ~' / ' 10%);\n  --popover-color-border: var(--popover-color-shadow);\n  --popover-shadow: 0 0 0 1px var(--popover-color-shadow), var(--bs-card);\n}\n\n//  $  POPOVER\n//  ----------------------------------------------------------------------------\n.d-popover {\n\n\n  //  $  POPOVER DIALOG\n  //  ----------------------------------------------------------------------------\n  &__dialog {\n    &,\n    *,\n    *::before,\n    *::after {\n      box-sizing: border-box;\n    }\n\n    display: grid;\n    grid-template-rows: 1fr;\n    overflow: auto;\n    background-color: var(--popover-color-background);\n    border-radius: var(--popover-border-radius);\n    box-shadow: var(--popover-shadow);\n\n    // to be set on the dialog when it is modal\n    &--modal {\n      z-index: var(--zi-modal-element);\n    }\n  }\n\n  //  $$  DIALOG CONTENT\n  //  ----------------------------------------------------------------------------\n  &__content {\n    overflow: auto;\n  }\n\n  //  $$  DIALOG HEADER / FOOTER\n  //  ----------------------------------------------------------------------------\n\n  &__header,\n  &__footer {\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n    width: 100%;\n    min-height: var(--size48);\n    padding-top: calc(var(--space-300) + var(--space-200)); // 6\n    padding-bottom: calc(var(--space-300) + var(--space-200)); // 6\n    overflow: auto;\n    font-weight: var(--fw-semibold);\n    font-size: var(--fs-200);\n  }\n\n  &__header {\n    border-bottom: var(--popover-border-width) solid var(--popover-color-border);\n  }\n\n  &__footer {\n    border-top: var(--popover-border-width) solid var(--popover-color-border);\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: RADIOS & CHECKBOXES\n//\n//  These are input classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/inputs\n//\n//  TABLE OF CONTENTS\n//  • SHARED STYLES\n//  • WRAPPERS\n//  • INPUT & COPY BLOCKS\n//  • LABELS\n//  • CHECKBOXES\n//    - Modifications\n//    - Validation States\n//  • RADIOS\n//    - Modifications\n//    - Validation States\n//\n//  ============================================================================\n//  $   RADIOS & CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox,\n.d-radio {\n    //  Component specific CSS Vars\n    //  ------------------------------------------------------------------------\n    --check-radio-color: var(--purple-400);\n    --check-radio-color-border: var(--black-600);\n    --check-radio-color-background: var(--white);\n\n    //  [1] Check to see if we can use custom styles, if so reset the defaults\n    //  ------------------------------------------------------------------------\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        width: var(--size-500); // 16\n        height: var(--size-500); // 16\n\n        //  [2]  Now re-style the checkboxes and radios\n        //  --------------------------------------------------------------------\n        margin: 0;\n        font-size: inherit;\n        background-color: var(--check-radio-color-background);\n        border: var(--size-100) solid var(--check-radio-color-border);\n        outline: 0;\n        box-shadow: none;\n        cursor: pointer;\n        -webkit-appearance: none;\n           -moz-appearance: none;\n                appearance: none;\n\n        &::-ms-check {\n            display: none;\n        }\n\n        &:focus,\n        &:checked:focus {\n          --check-radio-color-border: var(--check-radio-color);\n\n          outline: 0;\n          box-shadow: var(--bs-focus-ring);\n        }\n\n        &:checked {\n            --check-radio-color-border: var(--check-radio-color);\n        }\n    }\n\n    flex: 0 auto;\n    align-self: flex-start;\n    transition-timing-function: var(--ttf-out-quint);\n    transition-duration: var(--td300);\n    transition-property: box-shadow;\n\n    //  --  DISABLED\n    &[disabled],\n    &--disabled {\n        --check-radio-color: var(--fc-disabled);\n        --check-radio-color-border: var(--black-400);\n        --check-radio-color-background: var(--black-300);\n\n        cursor: not-allowed;\n    }\n}\n\n\n//  ============================================================================\n//  $   RADIO / CHECKBOX WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-checkbox-group,\n.d-radio-group {\n    display: flex;\n    gap: var(--space-400); // 8\n    padding-right: var(--space-100); // 1\n    padding-bottom: var(--space-200); // 2\n    padding-left: var(--space-100); // 1\n\n    //  --  Wrapper Disabled State\n    &.d-checkbox-group--disabled,\n    &.d-radio-group--disabled {\n        .d-checkbox__label,\n        .d-radio__label {\n            color: var(--fc-disabled);\n            cursor: not-allowed;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   INPUT BLOCKS\n//      This holds the radio or checkbox input\n//  ----------------------------------------------------------------------------\n.d-checkbox__input,\n.d-radio__input {\n    display: flex;\n    align-self: flex-start;\n    padding-top: calc(var(--space-200) + var(--space-100)); // 3\n}\n\n\n//  ============================================================================\n//  $   COPY BLOCKS\n//      This holds the label and description copy\n//  ----------------------------------------------------------------------------\n.d-checkbox__copy,\n.d-radio__copy {\n    display: inline-flex;\n    flex-direction: column;\n}\n\n.d-checkbox__label,\n.d-radio__label {\n    display: inline-flex;\n    flex: 1 auto;\n    flex-direction: column;\n    color: var(--black-800);\n    font-weight: var(--fw-normal);\n    font-size: var(--fs-200);\n    line-height: calc(20/14);\n    cursor: pointer;\n}\n\n\n//  ============================================================================\n//  $   CHECKBOXES\n//  ----------------------------------------------------------------------------\n.d-checkbox {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        background-repeat: no-repeat;\n        background-position: center center;\n        background-size: contain;\n        border-radius: var(--size-300);\n\n        &:focus-visible,\n        &:checked:focus-visible {\n            box-shadow: var(--bs-focus-ring);\n        }\n\n        &:checked {\n            --check-radio-color-background: var(--check-radio-color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            //  Disabled\n            &[disabled] {\n                --check-radio-color-background: var(--black-300);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio-color-background: var(--black-300);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        &--indeterminate,\n        &:indeterminate {\n            --check-radio-color-background: var(--check-radio-color);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='%23fff'/%3E%3C/svg%3E\");\n\n            &[disabled] {\n                --check-radio-color-background: var(--black-300);\n\n                background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n            }\n        }\n\n        &--indeterminate&--disabled,\n        &:indeterminate&--disabled {\n            --check-radio-color-background: var(--black-300);\n\n            background-image: url(\"data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 13H5v-2h14v2z' fill='hsl(240, 10%, 51%)'/%3E%3C/svg%3E\");\n        }\n\n        //  Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio-color: var(--fc-disabled);\n            --check-radio-color-border: var(--black-400);\n            --check-radio-color-background: var(--black-300);\n        }\n    }\n}\n\n//  $$  CHECKBOX VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-checkbox--warning {\n    --check-radio-color: var(--gold-500);\n    --check-radio-color-border: var(--check-radio-color);\n}\n\n.d-checkbox--error {\n    --check-radio-color: var(--red-300);\n    --check-radio-color-border: var(--check-radio-color);\n}\n\n.d-checkbox--success {\n    --check-radio-color: var(--green-400);\n    --check-radio-color-border: var(--check-radio-color);\n}\n\n//  ============================================================================\n//  $   RADIOS\n//  ----------------------------------------------------------------------------\n.d-radio {\n    @supports ((-webkit-appearance: none) or (-moz-appearance: none) or (appearance: none)) {\n        border-radius: var(--br-circle);\n\n        // Disabled\n        &[disabled],\n        &--disabled {\n            --check-radio-color: var(--fc-disabled);\n            --check-radio-color-border: var(--black-400);\n            --check-radio-color-background: var(--black-300);\n        }\n\n        &:focus-visible,\n        &:checked:focus-visible {\n          box-shadow: var(--bs-focus-ring), inset 0 0 0 var(--size-200) var(--white);\n        }\n\n        &:checked {\n            --check-radio-color-background: var(--check-radio-color);\n\n            box-shadow: inset 0 0 0 var(--size-200) var(--white);\n\n            // Disabled\n            &[disabled] {\n                --check-radio-color-background: var(--black-400);\n            }\n        }\n\n        &--disabled:checked {\n            --check-radio-color-background: var(--black-400);\n        }\n    }\n}\n\n\n//  $$  RADIO VALIDATION STATES\n//  ----------------------------------------------------------------------------\n.d-radio--warning {\n    --check-radio-color: var(--gold-500);\n    --check-radio-color-border: var(--check-radio-color);\n}\n\n.d-radio--error {\n    --check-radio-color: var(--red-300);\n    --check-radio-color-border: var(--check-radio-color);\n}\n\n.d-radio--success {\n    --check-radio-color: var(--green-400);\n    --check-radio-color-border: var(--check-radio-color);\n}\n","//@import (reference) '../variables/sizes.less';\n//@import (reference) '../variables/icons.less';\n//@import (reference) 'input.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: SELECT MENUS\n//\n//  These are select menu classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/select\n//\n//  TABLE OF CONTENTS\n//  • SELECT MENUS\n//    - Sizes\n//  • SELECT MENU INPUTS\n//    - Validation States\n//\n\n//  ============================================================================\n//  $  SELECT MENU (NATIVE)\n//  ----------------------------------------------------------------------------\n.d-select {\n  //  Component CSS Vars\n  //  --------------------------------------------------------------------------\n  --select-color-border: var(--black-500);\n  --select-notch-position-right: calc(var(--size-300) * 2);\n\n  position: relative;\n  width: stretch;\n\n  //  --  Arrows\n  &::after {\n    --select-arrow-size: calc(var(--size-300) * 3.5);\n\n    position: absolute;\n    top: 50%;\n    right: var(--select-notch-position-right);\n    z-index: var(--zi-selected);\n    width: var(--select-arrow-size);\n    height: var(--select-arrow-size);\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n    background-size: 100%;\n    transform: translateY(-50%);\n    content: '';\n    pointer-events: none;\n  }\n}\n\n//  $  SELECT MENU INPUT\n//  ----------------------------------------------------------------------------\n.d-select__input {\n  --select-color-border: var(--input-color-border);\n  --select-notch-padding-right: calc(var(--space-300) * 6);\n\n  // Use shared style properties of d-input (<input>, <textarea>)\n  .d-input();\n\n  //  --  FOCUS STATE\n  &:focus {\n      --input-color-background: hsla(var(--black-900-hsl) / 0.03);\n  }\n\n  // Adjust space on right beneath arrows\n  padding-right: var(--select-notch-padding-right);\n\n  &[disabled],\n  &[read-only] {\n    color: var(--fc-disabled);\n    background: var(--input-color-background-disabled);\n    border-color: transparent;\n    border-style: solid;\n    border-width: var(--input-border-width);\n  }\n\n  &::-moz-focus-inner {\n    outline: none !important;\n  }\n\n  &:-moz-focusring {\n    color: transparent;\n    text-shadow: 0 0 0 var(--black-900);\n  }\n\n  &::-ms-expand {\n    display: none;\n  }\n}\n\n//  $$  SIZES\n//  ----------------------------------------------------------------------------\n.d-select--xs {\n  .d-select__input {\n    #d-internal__input-and-select-xs();\n  }\n}\n\n.d-select--sm {\n  .d-select__input {\n    #d-internal__input-and-select-sm();\n  }\n}\n\n.d-select--lg {\n  .d-select__input {\n    #d-internal__input-and-select-lg();\n\n    --select-notch-padding-right: calc(var(--space-300) * 9);\n  }\n\n  &::after {\n    --select-arrow-size: calc(var(--size-300) * 4.5);\n    --select-notch-position-right: calc(var(--size-300) * 3);\n  }\n}\n\n.d-select--xl {\n  .d-select__input {\n    #d-internal__input-and-select-xl();\n\n    --select-notch-padding-right: calc(var(--space-300) * 9);\n  }\n\n  &::after {\n    --select-arrow-size: calc(var(--size-300) * 4.5);\n    --select-notch-position-right: calc(var(--size-300) * 3);\n  }\n}\n\n//  $$  VALIDATION STATES\n//  ------------------------------------------------------------------------\n.d-select__input--success {\n  --input-color-border: var(--green-400);\n}\n\n.d-select__input--error {\n  --input-color-border: var(--red-300);\n}\n\n.d-select__input--warning {\n  --input-color-border: var(--gold-400);\n}\n\n.d-select--disabled {\n  &::before,\n  &::after {\n    opacity: 0.33;\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: SKELETON\n//\n//  These are the styles for skeleton component.\n//  The --placeholder-from-color and --placeholder-to-color custom properties can be set on the parent class of the\n//  placeholder to control the animation colors.\n//  For further documentation of these styles, please visit https://dialpad.design/components/skeleton\n\n//  ============================================================================\n//  @   SKELETON CONTAINER\n//  ----------------------------------------------------------------------------\n.skeleton-placeholder {\n  display: flex;\n  width: 100%;\n  background: var(--placeholder-from-color, var(--black-300));\n  animation-duration: 1000ms;\n  fill: var(--placeholder-from-color, var(--black-300));\n  stroke: none;\n\n  &--animate {\n    animation-name: placeholder-throb;\n    animation-iteration-count: infinite;\n  }\n}\n\n// the animation is used by the skeleton component\n@keyframes placeholder-throb {\n  10% {\n    background: var(--placeholder-from-color, var(--black-300));\n    fill: var(--placeholder-from-color, var(--black-300));\n  }\n\n  50% {\n    background: var(--placeholder-to-color, var(--black-200));\n    fill: var(--placeholder-to-color, var(--black-200));\n  }\n\n  90% {\n    background: var(--placeholder-from-color, var(--black-300));\n    fill: var(--placeholder-from-color, var(--black-300));\n  }\n}\n","//@import (reference) '../utilities/typography.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TABLES\n//\n//  These are table component classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tables\n//\n//  TABLE OF CONTENTS\n//  • DEFAULT STYLE\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-table {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --table-color-border: var(--black-300);\n    --table-th-color-text: var(--fc-secondary);\n    --table-td-color-text: var(--fc-tertiary);\n\n    display: table;\n    box-sizing: border-box;\n    width: 100%;\n    max-width: 100%;\n    margin: 0;\n    font-size: var(--fs-200);\n    line-height: var(--lh2);\n    border-collapse: collapse;\n    border-spacing: 0;\n\n    // Caption styles\n    .d-table__caption {\n      margin-bottom: var(--space-400); // 8\n      color: var(--table-th-color-text);\n      font-weight: var(--fw-bold);\n      font-size: var(--fs-200);\n      .d-ta-unset();\n    }\n\n    // Table Head Styles\n    thead {\n        border-bottom: var(--size-200) solid var(--table-color-border);\n\n        // Column Header Styles\n        th {\n            font-size: var(--fs-100);\n            text-transform: uppercase;\n        }\n    }\n\n    // Header Styles\n    th {\n        color: var(--table-th-color-text);\n        font-weight: var(--fw-bold);\n    }\n\n    // Cell Styles\n    td {\n        color: var(--table-td-color-text);\n    }\n\n    // Header & Cell Styles\n    th,\n    td {\n        padding: var(--space-500); // 16\n        text-align: left;\n        border-bottom: var(--size-100) solid var(--table-color-border);\n    }\n\n    // Nested Table Body, First Row Styles, used internally on the Dialtone site\n    tbody + tbody tr:first-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-top: var(--size-200) solid var(--table-color-border);\n        }\n    }\n\n    // Last Table Row Styles\n    tbody tr:last-of-type {\n        // Header & Cell Styles\n        td,\n        th {\n            border-bottom-width: 0;\n        }\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-table--inverted {\n    --table-th-color-text: var(--fc-secondary-inverted);\n    --table-td-color-text: var(--black-400);\n    --table-color-border: var(--black-500);\n}\n\n//  ============================================================================\n//  $   STRIPED ROW BACKGROUNDS\n//  ----------------------------------------------------------------------------\n.d-table--striped {\n    // Row Styles\n    tr:nth-child(even) {\n        background-color: hsla(var(--bgc-bold-hsl) / 0.1);\n    }\n\n    &.d-table--inverted {\n        // Row Styles\n        tr:nth-child(even) {\n            background-color: hsla(var(--bgc-primary-hsl) / 0.08);\n        }\n    }\n}\n","//@import (reference) '../variables/typography.less';\n//@import (reference) '../variables/spacing.less';\n//@import (reference) '../utilities/internals.less';\n//@import (reference) '../utilities/grid.less';\n\n//\n//  DIALTONE\n//  UTILTIES: TYPOGRAPHY\n//\n//  These are utility typography classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilties/typography\n//\n//  TABLE OF CONTENTS\n//  • FONT-FACE\n//  • FONT FAMILY\n//  • RESET HEADERS & PARAGRAPH MARGINS\n//  • FONT SIZE\n//  • FONT STYLE\n//  • FONT WEIGHT\n//  • LINE HEIGHT\n//  • LISTS\n//  • TEXT ALIGN\n//  • TEXT DECORATION\n//  • TEXT OVERFLOW\n//  • TEXT TRANSFORM\n//  • VERTICAL ALIGN\n//  • WHITESPACE\n//  • WORDBREAK\n//\n//  ============================================================================\n//  $   FONT FACE\n//      To turn on custom webfonts, set @define-font-face to true.\n//  ----------------------------------------------------------------------------\n#d-internals #font-face(@define-ff-custom-adjusted, @define-ff-custom-adjusted-name, normal, true, 95%);\n#d-internals #font-face(@define-ff-custom-adjusted-italic, @define-ff-custom-adjusted-name, italic, true, 95%);\n#d-internals #font-face(@define-ff-mono, @define-ff-mono-name, normal, true);\n#d-internals #font-face(@define-ff-marketing, @define-ff-marketing-name);\n#d-internals #font-face(@define-ff-marketing-semi-expanded, @define-ff-marketing-semi-expanded-name);\n#d-internals #font-face(@define-ff-marketing-expanded, @define-ff-marketing-expanded-name);\n\n@font-face {\n    font-family: 'Segoe UI Adjusted';\n    src: local(Segoe UI Bold);\n    ascent-override: 90%;\n    font-weight: 700;\n}\n//  ============================================================================\n//  $   FONT FAMILY\n//  ----------------------------------------------------------------------------\n.d-ff-custom { font-family: @ff-custom !important; }\n.d-ff-sans { font-family: @ff-sans !important; }\n.d-ff-mono { font-family: @ff-mono !important; }\n.d-ff-marketing { font-family: @ff-marketing !important; }\n.d-ff-marketing-semi-expanded { font-family: @ff-marketing-semi-expanded !important; }\n.d-ff-marketing-expanded { font-family: @ff-marketing-expanded !important; }\n.d-ff-unset { font-family: unset !important; }\n\n//  ============================================================================\n//  $   RESET HEADERS & PARAGRAPH MARGINS\n//  ----------------------------------------------------------------------------\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nol,\nul {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\n//  ============================================================================\n//  $   BODY STYLES\n//  ----------------------------------------------------------------------------\n.d-body-base          { line-height: var(--lh-400); font-size: var(--fs-200); }\n.d-body-small         { line-height: var(--lh-300); font-size: var(--fs-100); }\n.d-body-compact       { line-height: var(--lh-300); font-size: var(--fs-200); }\n.d-body-compact-small { line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   LABEL STYLES\n//  ----------------------------------------------------------------------------\n.d-label-base                { line-height: var(--lh-400); font-size: var(--fs-200); font-weight: var(--fw-semibold); }\n.d-label-small               { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-semibold); }\n.d-label-compact             { line-height: var(--lh-300); font-size: var(--fs-200); font-weight: var(--fw-semibold); }\n.d-label-plain               { line-height: var(--lh-400); font-size: var(--fs-200);                                  }\n.d-label-compact-plain       { line-height: var(--lh-300); font-size: var(--fs-200);                                  }\n.d-label-compact-small       { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-semibold); }\n.d-label-plain-small         { line-height: var(--lh-300); font-size: var(--fs-100);                                  }\n.d-label-compact-plain-small { line-height: var(--lh-200); font-size: var(--fs-100);                                  }\n\n//  ============================================================================\n//  $   HELPER STYLES\n//  ----------------------------------------------------------------------------\n.d-helper-base  { line-height: var(--lh-300); font-size: var(--fs-200); }\n.d-helper-small { line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   CODE STYLES\n//  ----------------------------------------------------------------------------\n\n.d-code-base  { font-family: var(--ff-mono); line-height: var(--lh-200); font-size: var(--fs-200); }\n.d-code-small { font-family: var(--ff-mono); line-height: var(--lh-200); font-size: var(--fs-100); }\n\n//  ============================================================================\n//  $   FONT SIZE\n//  ----------------------------------------------------------------------------\n.d-fs-100 { font-size: var(--fs-100) !important; }\n.d-fs-200 { font-size: var(--fs-200) !important; }\n.d-fs-300 { font-size: var(--fs-300) !important; }\n.d-fs-400 { font-size: var(--fs-400) !important; }\n.d-fs-500 { font-size: var(--fs-500) !important; }\n\n.d-fs-100-mobile { font-size: var(--fs-100-mobile) !important; }\n.d-fs-200-mobile { font-size: var(--fs-200-mobile) !important; }\n.d-fs-300-mobile { font-size: var(--fs-300-mobile) !important; }\n.d-fs-400-mobile { font-size: var(--fs-400-mobile) !important; }\n.d-fs-500-mobile { font-size: var(--fs-500-mobile) !important; }\n\n.d-fs-100-tc8 { font-size: var(--fs-100-tc8) !important; }\n.d-fs-200-tc8 { font-size: var(--fs-200-tc8) !important; }\n.d-fs-300-tc8 { font-size: var(--fs-300-tc8) !important; }\n.d-fs-400-tc8 { font-size: var(--fs-400-tc8) !important; }\n.d-fs-500-tc8 { font-size: var(--fs-500-tc8) !important; }\n\n.d-fs-100-tv { font-size: var(--fs-100-tv) !important; }\n.d-fs-200-tv { font-size: var(--fs-200-tv) !important; }\n.d-fs-300-tv { font-size: var(--fs-300-tv) !important; }\n.d-fs-400-tv { font-size: var(--fs-400-tv) !important; }\n.d-fs-500-tv { font-size: var(--fs-500-tv) !important; }\n\n.d-headline10 { .d-fs-100(); font-weight: @headline-weight; .d-tt-uppercase(); }\n.d-headline24 { .d-fs-300(); font-weight: @headline-weight !important; }\n.d-headline28 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline32 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline36 { .d-fs-400(); font-weight: @headline-weight !important; }\n.d-headline42 { .d-fs-500(); font-weight: @headline-weight !important; }\n.d-headline48 { .d-fs-500(); font-weight: @headline-weight !important; }\n.d-headline54 { .d-fs-500(); font-weight: @headline-weight !important; }\n\n.d-headline-eyebrow            { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-normal); .d-tt-uppercase(); }\n.d-headline-small              { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-bold); }\n.d-headline-soft-small         { line-height: var(--lh-300); font-size: var(--fs-100); font-weight: var(--fw-medium); }\n.d-headline-medium             { line-height: var(--lh-400); font-size: var(--fs-200); font-weight: var(--fw-bold); }\n.d-headline-compact-small      { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-bold); }\n.d-headline-compact-soft-small { line-height: var(--lh-200); font-size: var(--fs-100); font-weight: var(--fw-medium); }\n.d-headline-compact-base,\n.d-headline-compact-medium     { line-height: var(--lh-300); font-size: var(--fs-200); font-weight: var(--fw-bold); }\n.d-headline-compact-large      { line-height: var(--lh-200); font-size: var(--fs-300); font-weight: var(--fw-bold); }\n.d-headline-large              { line-height: var(--lh-400); font-size: var(--fs-300); font-weight: var(--fw-bold); }\n.d-headline-extra-large        { line-height: var(--lh-200); font-size: var(--fs-400); font-weight: var(--fw-medium); }\n.d-headline-extra-extra-large  { line-height: var(--lh-200); font-size: var(--fs-500); font-weight: var(--fw-medium); }\n\n.d-fs-unset { font-size: unset !important; }\n\n//  ============================================================================\n//  $$  FONT STYLE\n//  ----------------------------------------------------------------------------\n.d-fs-normal { font-style: normal !important; }\n.d-fs-italic { font-style: italic !important; }\n.d-fs-unset { font-style: unset !important; }\n\n\n//  ============================================================================\n//  $$  FONT WEIGHT\n//  ----------------------------------------------------------------------------\n.d-fw-normal { font-weight: var(--fw-normal) !important; }\n.d-fw-medium { font-weight: var(--fw-medium) !important; }\n.d-fw-semibold { font-weight: var(--fw-semibold) !important; }\n.d-fw-bold { font-weight: var(--fw-bold) !important; }\n.d-fw-unset { font-weight: unset !important; }\n\n\n//  ============================================================================\n//  $  LINE HEIGHT\n//  ----------------------------------------------------------------------------\n.d-lh0 { line-height: var(--lh0) !important; }\n.d-lh1 { line-height: var(--lh1) !important; }\n.d-lh2 { line-height: var(--lh2) !important; }\n.d-lh4 { line-height: var(--lh4) !important; }\n.d-lh6 { line-height: var(--lh6) !important; }\n.d-lh8 { line-height: var(--lh8) !important; }\n.d-lh12 { line-height: var(--lh12) !important; }\n.d-lh16 { line-height: var(--lh16) !important; }\n.d-lh20 { line-height: var(--lh20) !important; }\n.d-lh24 { line-height: var(--lh24) !important; }\n.d-lh-100 { line-height: var(--lh-100) !important; }\n.d-lh-200 { line-height: var(--lh-200) !important; }\n.d-lh-300 { line-height: var(--lh-300) !important; }\n.d-lh-400 { line-height: var(--lh-400) !important; }\n.d-lh-500 { line-height: var(--lh-500) !important; }\n.d-lh-600 { line-height: var(--lh-600) !important; }\n.d-lh-unset { line-height: unset !important; }\n\n\n//  ============================================================================\n//  $   LISTS\n//  ----------------------------------------------------------------------------\n.d-ls-reset {\n    //  We want to reset everything when applied\n    &,\n    li,\n    ol,\n    ul {\n        margin: @su0;\n        padding: @su0;\n        .d-ls-none();\n    }\n}\n.d-ls-none { list-style: none !important; }\n.d-lst-disc { list-style-type: disc !important; }\n.d-lst-circle { list-style-type: circle !important; }\n.d-lst-content { list-style-type: var(--ls-content) !important; }\n.d-lst-decimal { list-style-type: decimal !important; }\n.d-lst-none { list-style-type: none !important; }\n\n\n//  ============================================================================\n//  $$  TEXT ALIGN\n//  ----------------------------------------------------------------------------\n.d-ta-left { text-align: left !important; }\n.d-ta-right { text-align: right !important; }\n.d-ta-center { text-align: center !important; }\n.d-ta-justify { text-align: justify !important; }\n.d-ta-unset { text-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT DECORATION\n//  ----------------------------------------------------------------------------\n.d-td-none { text-decoration: none !important; }\n.d-td-underline { text-decoration: underline !important; }\n.d-td-dotted { text-decoration: underline dotted !important; }\n.d-td-line-through { text-decoration: line-through !important; }\n.d-td-unset { text-decoration: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT OVERFLOW\n//  ----------------------------------------------------------------------------\n.d-to-ellipsis { text-overflow: ellipsis !important; }\n.d-to-clip { text-overflow: clip !important; }\n.d-to-unset { text-overflow: unset !important; }\n\n//  $$  OVERFLOW WRAP\n//  ----------------------------------------------------------------------------\n.d-ow-normal { overflow-wrap: normal !important; }\n.d-ow-anywhere { overflow-wrap: anywhere !important; }\n.d-ow-break-word { overflow-wrap: break-word !important; }\n.d-ow-unset { overflow-wrap: unset !important; }\n\n\n//  ============================================================================\n//  $$  TEXT TRANSFORM\n//  ----------------------------------------------------------------------------\n.d-tt-uppercase { text-transform: uppercase !important; }\n.d-tt-lowercase { text-transform: lowercase !important; }\n.d-tt-capitalize { text-transform: capitalize !important; }\n.d-tt-full-width { text-transform: full-width !important; }\n.d-tt-none { text-transform: none !important; }\n.d-tt-unset { text-transform: unset !important; }\n\n\n//  ============================================================================\n//  $$  VERTICAL ALIGN\n//  ----------------------------------------------------------------------------\n.d-va-baseline { vertical-align: baseline !important; }\n.d-va-top { vertical-align: top !important; }\n.d-va-bottom { vertical-align: bottom !important; }\n.d-va-text-top { vertical-align: text-top !important; }\n.d-va-text-bottom { vertical-align: text-bottom !important; }\n.d-va-middle { vertical-align: middle !important; }\n.d-va-sub { vertical-align: sub !important; }\n.d-va-super { vertical-align: super !important; }\n.d-va-unset { vertical-align: unset !important; }\n\n\n//  ============================================================================\n//  $$  WHITESPACE\n//  ----------------------------------------------------------------------------\n.d-ws-normal { white-space: normal !important; }\n.d-ws-nowrap { white-space: nowrap !important; }\n.d-ws-pre { white-space: pre !important; }\n.d-ws-pre-line { white-space: pre-line !important; }\n.d-ws-pre-wrap { white-space: pre-wrap !important; }\n.d-ws-break-spaces { white-space: break-spaces !important; }\n.d-ws-unset { white-space: unset !important; }\n\n\n//  ============================================================================\n//  $$  WORD BREAK\n//  ----------------------------------------------------------------------------\n.d-wb-normal { word-break: normal !important; }\n.d-wb-break-all { word-break: break-all !important; }\n.d-wb-keep-all { word-break: keep-all !important; }\n.d-wb-unset { word-break: unset !important; }\n\n\n\n// Unsure what to do with this\n//  $$  TRUNCATE\n//  ----------------------------------------------------------------------------\n.d-truncate {\n    overflow: hidden !important;\n    white-space: nowrap !important;\n    text-overflow: ellipsis !important;\n}\n","//\n//  DIALTONE\n//  COMPONENTS: TABS\n//\n//  These are tab classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tabs\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//\n//  ============================================================================\n//  $   WRAPPERS\n//  ----------------------------------------------------------------------------\n.d-tablist {\n    position: relative;\n    display: flex;\n    flex-wrap: wrap;\n    color: var(--fc-tertiary);\n    font-size: var(--fs-200);\n\n    &:focus {\n        outline: 0;\n    }\n\n    // Add a bottom border unless no border is requested\n    &:not(.d-tablist--no-border)::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        z-index: var(--zi-base1);\n        height: var(--size-100);\n        background-color: var(--black-400);\n        content: '';\n    }\n}\n\n\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tab {\n    //  --  COMPONENT CSS VARS\n    //  ------------------------------------------------------------------------\n    --tab-color-background: hsla(var(--white-hsl) / 0);\n    --tab-color-text: inherit;\n\n    position: relative;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    box-sizing: border-box;\n    margin: 0 var(--space-200);\n    padding: calc(var(--space-300) * 3); // 12\n    color: var(--tab-color-text);\n    font: inherit;\n    line-height: 1;\n    background-color: var(--tab-color-background);\n    border: 0;\n    border-radius: var(--size-300) var(--size-300) 0 0; // 4 4 0 0\n    cursor: pointer;\n    transition-timing-function: var(--ttf-out-quint);\n    transition-duration: var(--td300);\n    transition-property: background-color, border, color, box-shadow;\n    fill: currentColor;\n\n    &:first-of-type {\n        margin-left: 0;\n    }\n\n    &:last-of-type {\n        margin-right: 0;\n    }\n\n    &::after {\n        position: absolute;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        height: var(--size-200);\n        background-color: var(--tab-color-background);\n        content: '';\n    }\n\n    &:focus-visible {\n        outline: none;\n        box-shadow: var(--bs-focus-ring-inset);\n    }\n\n    &:hover {\n        --tab-color-background: var(--black-100);\n        --tab-color-text: var(--fc-primary);\n    }\n\n    //  Turn off animations if someone doesn't want them.\n    @media (prefers-reduced-motion) {\n        transition: none;\n    }\n}\n\n\n//  ============================================================================\n//  $   SELECTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tab--selected {\n    --tab-color-text: var(--purple-400);\n\n    z-index: var(--zi-selected);\n\n    &:hover {\n        --tab-color-background: hsla(var(--purple-400-hsl) / 0.1);\n        --tab-color-text: hsl(var(--purple-400-h) var(--purple-400-s) calc(var(--purple-400-l) - 10%));\n    }\n\n    &::after,\n    &:hover::after {\n        --tab-color-background: var(--purple-400);\n    }\n}\n\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tablist--inverted {\n    --tab-color-text: var(--fc-primary-inverted);\n\n    &::after {\n        background-color: hsla(var(--purple-600-hsl) / 0.5);\n    }\n\n    .d-tab {\n        --tab-color-text: var(--fc-secondary-inverted);\n\n        &:hover {\n            --tab-color-background: hsla(var(--white-hsl) / 0.1);\n        }\n    }\n\n    .d-tab--selected {\n        --tab-color-text: var(--fc-primary-inverted);\n\n        &::after {\n            --tab-color-background: currentColor;\n        }\n    }\n}\n\n\n//  ============================================================================\n//  $   SIZES\n//  ----------------------------------------------------------------------------\n.d-tablist--sm {\n    font-size: var(--fs-100);\n\n    .d-tab {\n        padding: calc(var(--space-300) * 3); // 12\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: TOOLTIPS\n//\n//  These are tooltip classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/tooltips\n//\n//  TABLE OF CONTENTS\n//  • VARIABLES\n//  • BASE STYLE\n//  • ALTERNATE STYLES\n//  • ARROW DIRECTIONS\n//    - TOP LEFT\n//    - TOP CENTER\n//    - TOP RIGHT\n//    - RIGHT TOP\n//    - RIGHT CENTER\n//    - RIGHT BOTTOM\n//    - BOTTOM LEFT\n//    - BOTTOM CENTER\n//    - BOTTOM RIGHT\n//    - LEFT TOP\n//    - LEFT CENTER\n//    - LEFT BOTTOM\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip-container {\n  position: relative;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.d-tooltip {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip-color-background: var(--black-800);\n    --tooltip-color-text: var(--fc-primary-inverted);\n    --tooltip-font-weight: var(--fw-medium);\n    --tooltip-line-height: var(--lh-300);\n    --tooltip-padding-y: var(--space-400);\n    --tooltip-padding-x: calc(var(--space-300) * 3);\n    --tooltip-border-radius: var(--size-300);\n\n    z-index: var(--zi-tooltip);\n    max-width: calc(var(--size-300) * 54); // 216\n    padding: var(--tooltip-padding-y) var(--tooltip-padding-x); // 8 12\n    color: var(--tooltip-color-text);\n    font-weight: var(--tooltip-font-weight);\n    font-size: var(--fs-100);\n    line-height: var(--tooltip-line-height);\n    letter-spacing: calc(var(--size-100) * 0.25);\n    text-align: center;\n    background-color: var(--tooltip-color-background);\n    border-radius: var(--tooltip-border-radius); // 4\n\n    &::after {\n        position: absolute;\n        border: calc(var(--size-300) + var(--size-200)) solid transparent; // 6\n        content: '';\n    }\n}\n\n//  ============================================================================\n//  $   INVERTED STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--inverted {\n    //  --  COMPONENT CSS VARS\n    //  -----------------------\n    --tooltip-color-background: var(--black-200);\n    --tooltip-color-text: var(--fc-secondary);\n}\n\n//  ============================================================================\n//  $   HIDE STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--hide {\n    visibility: hidden;\n    opacity: 0;\n    transition:\n        visibility 0s 200ms,\n        transform 200ms var(--ttf-in-out) 0s,\n        opacity 200ms var(--ttf-in-out) 0s;\n}\n\n//  ============================================================================\n//  $   SHOW STYLE\n//  ----------------------------------------------------------------------------\n.d-tooltip--show {\n    transform: translate(0, 0);\n    visibility: visible;\n    opacity: 1;\n    transition:\n        visibility 0s 0s,\n        transform 200ms var(--ttf-in-out) 10ms,\n        opacity 200ms var(--ttf-in-out) 10ms;\n}\n\n//  ============================================================================\n//  $   HOVER AND FOCUS STYLES\n//  ----------------------------------------------------------------------------\n.d-tooltip--hover {\n    .d-tooltip {\n        &:extend(.d-tooltip--hide);\n    }\n\n    &:hover,\n    &:focus-visible {\n        .d-tooltip {\n            &:extend(.d-tooltip--show);\n        }\n    }\n}\n\n//  ============================================================================\n//  $   TOOLTIP DIRECTIONS\n//  ============================================================================\n//  $$  TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--top-right {\n  top: calc(100% + calc(var(--space-300) * 3)); // 100% + 12\n  transform: translateY(var(--space-500)); // 16\n\n  &::after {\n    top: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n    border-top-width: 0;\n    border-bottom-color: var(--tooltip-color-background);\n  }\n}\n\n//  $$  BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--bottom-left,\n.d-tooltip__arrow--bottom-center,\n.d-tooltip__arrow--bottom-right {\n  bottom: calc(100% + calc(var(--space-300) * 3)); // 100% + 12\n  transform: translateY(calc(var(--space-500) * -1)); // -16\n\n  &::after {\n    bottom: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n    border-top-color: var(--tooltip-color-background);\n    border-bottom-width: 0;\n  }\n}\n\n//  $$  TOP / BOTTOM LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-start,\n.d-tooltip__arrow-tippy--top-start,\n.d-tooltip__arrow--top-left,\n.d-tooltip__arrow--bottom-left {\n  left: calc(var(--space-200) * -1); // -2\n\n  &::after {\n    left: var(--space-500); // 16\n  }\n}\n\n//  $$  TOP / BOTTOM CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom,\n.d-tooltip__arrow-tippy--top,\n.d-tooltip__arrow--top-center,\n.d-tooltip__arrow--bottom-center {\n  &::after {\n    left: 50%;\n    margin-left: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n  }\n}\n\n//  $$  TOP / BOTTOM RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--bottom-end,\n.d-tooltip__arrow-tippy--top-end,\n.d-tooltip__arrow--top-right,\n.d-tooltip__arrow--bottom-right {\n  right: calc(var(--space-200) * -1); // -2\n\n  &::after {\n    right: var(--space-500); // 16\n  }\n}\n\n//  $$  RIGHT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--right-bottom {\n  right: calc(100% + calc(var(--space-300) * 3)); // 100% + 12\n  transform: translateX(calc(var(--space-500) * -1)); // -16\n\n  &::after {\n    right: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n    border-right-width: 0;\n    border-left-color: var(--tooltip-color-background);\n  }\n}\n\n//  $$  LEFT\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow--left-top,\n.d-tooltip__arrow--left-center,\n.d-tooltip__arrow--left-bottom {\n  left: calc(100% + calc(var(--space-300) * 3)); // 100% + 12\n  transform: translateX(var(--space-500)); // 16\n\n  &::after {\n    left: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n    border-right-color: var(--tooltip-color-background);\n    border-left-width: 0;\n  }\n}\n\n//  $$  RIGHT / LEFT TOP\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-start,\n.d-tooltip__arrow-tippy--left-start,\n.d-tooltip__arrow--right-top,\n.d-tooltip__arrow--left-top {\n  top: calc(var(--space-100) * -1); // -1\n\n  &::after {\n    top: var(--space-400); // 8\n  }\n}\n\n//  $$  RIGHT / LEFT CENTER\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right,\n.d-tooltip__arrow-tippy--left,\n.d-tooltip__arrow--right-center,\n.d-tooltip__arrow--left-center {\n  &::after {\n    top: 50%;\n    margin-top: calc(calc(var(--space-300) + var(--space-200)) * -1); // -6\n  }\n}\n\n//  $$  RIGHT / LEFT BOTTOM\n//  ----------------------------------------------------------------------------\n.d-tooltip__arrow-tippy--right-end,\n.d-tooltip__arrow-tippy--left-end,\n.d-tooltip__arrow--right-bottom,\n.d-tooltip__arrow--left-bottom {\n  bottom: calc(var(--space-100) * -1); // -1\n\n  &::after {\n    bottom: var(--space-400); // 8\n  }\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: TOGGLE\n//\n//  These are toggle classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/toggle\n//\n//  TABLE OF CONTENTS\n//  • BASE STYLE\n//  • TOGGLE VARIANTS\n//  • CHECKED TOGGLE\n//  • INDETERMINATE TOGGLE\n//  • FOCUSED TOGGLE\n//  • DISABLED TOGGLE\n//\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-toggle {\n  //  Component specific CSS Vars\n  //  ------------------------------------------------------------------------\n  --toggle-transition-timing-function: var(--ttf-out-quint);\n  --toggle-transition-speed: var(--td300);\n  --toggle-color-background: var(--black-400);\n  --toggle-size-height: calc(var(--size-300) * 6); // 24\n  --toggle-size-width: calc(var(--size-300) * 12); // 48\n  --toggle-size-icon: var(--size-500); // 16\n\n  //  $$  BASE STYLE\n  //  ----------------------------------------------------------------------------\n  position: relative;\n  display: inline-block;\n  box-sizing: border-box;\n  width: var(--toggle-size-width);\n  height: var(--toggle-size-height);\n  padding: 0;\n  line-height: var(--lh4);\n  vertical-align: middle;\n  background-color: var(--toggle-color-background);\n  border: var(--size-100) solid var(--toggle-color-background);\n  border-radius: var(--toggle-size-width);\n  cursor: pointer;\n  transition-timing-function: var(--ttf-out-quint);\n  transition-duration: var(--td300);\n  transition-property: background-color, border, color, box-shadow;\n\n  &__inner {\n    position: absolute;\n    top: calc(var(--space-300) - var(--space-100)); // 3\n    left: calc(var(--space-300) * 6); // 24\n    width: var(--toggle-size-icon);\n    height: var(--toggle-size-icon);\n\n    .d-toggle--small & {\n      top: var(--space-100); // 1\n      left: calc(var(--space-500) + var(--space-100)); // 17\n    }\n\n    .d-toggle--indeterminate & {\n      display: none;\n    }\n\n    color: var(--white);\n    background-image: url('data:image/svg+xml;base64,');\n    background-repeat: no-repeat;\n    background-size: cover;\n  }\n\n  &::after {\n    position: absolute;\n    top: var(--space-100); // 1\n    left: var(--space-100); // 1\n    width: calc(var(--toggle-size-icon) + var(--size-300));\n    height: calc(var(--toggle-size-icon) + var(--size-300));\n    background-color: var(--white);\n    border-radius: var(--br-circle);\n    cursor: pointer;\n    transition: left var(--toggle-transition-speed) var(--toggle-transition-timing-function);\n    content: ' ';\n  }\n\n  //  $$  TOGGLE VARIANTS\n  //  ----------------------------------------------------------------------------\n  &--small {\n    --toggle-size-height: calc(var(--size-500) + var(--size-200)); // 18\n    --toggle-size-width: calc(var(--size-600) + var(--size-300)); // 36\n    --toggle-size-icon: calc(var(--size-500) - var(--size-200)); // 14\n\n    &::before {\n      position: absolute;\n      top: calc(var(--space-300) * -1); // -4\n      right: 0;\n      bottom: calc(var(--space-300) * -1); // -4\n      left: 0;\n      content: '';\n    }\n  }\n\n  &--small::after {\n    width: var(--toggle-size-icon);\n    height: var(--toggle-size-icon);\n  }\n\n  //  $$  CHECKED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--checked {\n    --toggle-color-background: var(--purple-400);\n\n    .d-toggle__inner {\n      left: calc(var(--space-300) + var(--space-200)); // 6\n      background-image: url('data:image/svg+xml;base64,');\n    }\n\n    &.d-toggle--small .d-toggle__inner {\n      left: calc(var(--space-100) + var(--space-200)); // 3\n    }\n\n    &::after {\n      left: calc(calc(var(--space-300) * 6) + var(--space-100)); // 25\n    }\n\n    &.d-toggle--small::after {\n      right: var(--space-100); // 1\n      left: calc(calc(var(--space-300) * 5) - var(--space-100)); // 19\n    }\n  }\n\n  //  $$  INDETERMINATE TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--indeterminate {\n    &__inner {\n      display: none;\n    }\n\n    &::after {\n      left: 50%;\n      transform: translate(-50%, 0);\n    }\n  }\n\n  //  $$  FOCUSED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &:focus-visible {\n    outline: none;\n    box-shadow: var(--bs-focus-ring);\n  }\n\n  //  $$  DISABLED TOGGLE\n  //  ----------------------------------------------------------------------------\n  &--disabled,\n  &[disabled] {\n    cursor: not-allowed;\n    opacity: 0.5;\n\n    &::after {\n      cursor: not-allowed;\n      transition-property: none;\n    }\n  }\n}\n\n.d-toggle-group {\n  gap: var(--space-400); // 8\n}\n","//@import (reference) '../variables/icons.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: PRESENCE\n//\n//  These are presence classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/presence\n\n.d-presence {\n  --presence-color-border-base: var(--black-100);\n  --presence-color-border-offline: var(--black-400);\n  --presence-color-background-base: var(--black-400);\n  --presence-color-background-active: var(--green-400);\n  --presence-color-background-busy: var(--red-300);\n  --presence-color-background-away: var(--gold-300);\n  --presence-color-background-offline: var(--white);\n  --presence-size: var(--size-200);\n\n  display: inline-block;\n  box-sizing: border-box;\n  border-color: var(--presence-color-border-base);\n  border-style: solid;\n  border-width: var(--presence-size);\n  border-radius: var(--br-circle);\n\n  &__inner {\n    box-sizing: border-box;\n    width: var(--size-400);\n    height: var(--size-400);\n    background-color: var(--presence-color-background-base);\n    border: none;\n    border-radius: 50%;\n  }\n\n  &__inner--active {\n    background-color: var(--presence-color-background-active);\n  }\n\n  &__inner--busy {\n    background-color: var(--presence-color-background-busy);\n  }\n\n  &__inner--away {\n    background-color: var(--presence-color-background-away);\n  }\n\n  &__inner--offline {\n    background-color: var(--presence-color-background-offline);\n    border-color: var(--presence-color-border-offline);\n    border-style: solid;\n    border-width: var(--size-200);\n  }\n}\n","//\n//  DIALTONE\n//  UTILITIES: ICONS\n//\n//  These are icon component classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/icon.html\n//\n//  TABLE OF CONTENTS\n//  • SIZE\n//\n//  ============================================================================\n//  $   SIZE\n//  ----------------------------------------------------------------------------\n.d-icon {\n  --icon-base-scale: var(--size-400); // replace with global variable eventually\n  --icon-size-100: calc(var(--icon-base-scale) * 1.5);\n  --icon-size-200: calc(var(--icon-base-scale) * 1.75);\n  --icon-size-300: calc(var(--icon-base-scale) * 2.25);\n  --icon-size-400: calc(var(--icon-base-scale) * 2.5);\n  --icon-size-500: calc(var(--icon-base-scale) * 3);\n  --icon-size-600: calc(var(--icon-base-scale) * 4);\n  --icon-size-700: calc(var(--icon-base-scale) * 4.75);\n  --icon-size-800: calc(var(--icon-base-scale) * 6);\n  --icon-size: var(--icon-size-500);\n\n  width: var(--icon-size);\n  height: var(--icon-size);\n  fill: currentColor;\n\n  &--size-800 {\n    --icon-size: var(--icon-size-800);\n  }\n\n  &--size-700 {\n    --icon-size: var(--icon-size-700);\n  }\n\n  &--size-600 {\n    --icon-size: var(--icon-size-600);\n  }\n\n  &--size-500 {\n    --icon-size: var(--icon-size-500);\n  }\n\n  &--size-400 {\n    --icon-size: var(--icon-size-400);\n  }\n\n  &--size-300 {\n    --icon-size: var(--icon-size-300);\n  }\n\n  &--size-200 {\n    --icon-size: var(--icon-size-200);\n  }\n\n  &--size-100 {\n    --icon-size: var(--icon-size-100);\n  }\n}\n","//\n//  DIALTONE\n//  TOKENS: ICONS\n//\n//  These are icon variables for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/\n//\n//  ============================================================================\n//  $   ICON SIZES\n//  ----------------------------------------------------------------------------\n//  OLD VARIABLES\n@icon-size14:     1.4rem;\n@icon-size16:     1.6rem;\n@icon-size18:     1.8rem;\n@icon-size20:     2rem;\n@icon-size24:     2.4rem;\n@icon-size32:     3.2rem;\n@icon-size48:     4.8rem;\n@icon-size64:     6.4rem;\n\n//  NEW VARIABLES\n@icon12:                        1.2rem;\n@icon14:                        1.4rem;\n@icon16:                        1.6rem;\n@icon18:                        1.8rem;\n@icon20:                        2rem;\n@icon24:                        2.4rem;\n@icon32:                        3.2rem;\n@icon48:                        4.8rem;\n@icon64:                        6.4rem;\n\n@icon-vars: {\n  icon12:                       @icon12;\n  icon14:                       @icon14;\n  icon16:                       @icon16;\n  icon18:                       @icon18;\n  icon20:                       @icon20;\n  icon24:                       @icon24;\n  icon32:                       @icon32;\n  icon48:                       @icon48;\n  icon64:                       @icon64;\n}\n","//@import (reference) '../variables/colors.less';\n//@import (reference) '../utilities/internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: BACKGROUNDS\n//\n//  These are border utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/borders\n//\n//  TABLE OF CONTENTS\n//  • BACKGROUND ATTACHMENT\n//  • BACKGROUND CLIP\n//  • BACKGROUND POSITION\n//  • BACKGROUND REPEAT\n//  • BACKGROUND SIZE\n//  • GRADIENTS\n//  • PATTERNS\n//\n//  ----------------------------------------------------------------------------\n//  --  Create background stops for most colors\n#d-internals #gradient-stops(black);\n#d-internals #gradient-stops(orange);\n#d-internals #gradient-stops(purple);\n#d-internals #gradient-stops(magenta);\n#d-internals #gradient-stops(gold);\n#d-internals #gradient-stops(red);\n#d-internals #gradient-stops(green);\n\n//  --  A few additional colors that the gradient-stops mixin misses\n#d-internals #gradient-classes(white);\n#d-internals #gradient-classes(black-900);\n#d-internals #gradient-classes(black-800);\n#d-internals #gradient-classes(black-700);\n\n//  ============================================================================\n//  $  BACKGROUND ATTACHMENT\n//  ----------------------------------------------------------------------------\n.d-bga-scroll { background-attachment: scroll !important; }\n.d-bga-fixed { background-attachment: fixed !important; }\n.d-bga-local { background-attachment: local !important; }\n.d-bga-unset { background-attachment: unset !important; }\n\n//  ============================================================================\n//  $  BACKGROUND CLIP\n//  ----------------------------------------------------------------------------\n.d-bgc-border-box { background-clip: border-box !important; }\n.d-bgc-padding-box { background-clip: padding-box !important; }\n.d-bgc-content-box { background-clip: content-box !important; }\n\n.d-bgc-text {\n  color: transparent !important;\n  -webkit-background-clip: text !important;\n  background-clip: text !important;\n}\n.d-bgclip-unset { background-clip: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND POSITION\n//  ----------------------------------------------------------------------------\n.d-bgp-t { background-position: top !important; }\n.d-bgp-tr { background-position: top right !important; }\n.d-bgp-r { background-position: right !important; }\n.d-bgp-br { background-position: bottom right !important; }\n.d-bgp-b { background-position: bottom !important; }\n.d-bgp-bl { background-position: bottom left !important; }\n.d-bgp-l { background-position: left !important; }\n.d-bgp-tl { background-position: top left !important; }\n.d-bgp-center { background-position: center !important; }\n.d-bgp-unset { background-position: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND REPEAT\n//  ----------------------------------------------------------------------------\n.d-bgr-repeat-x { background-repeat: repeat-x !important; }\n.d-bgr-repeat-y { background-repeat: repeat-y !important; }\n.d-bgr-repeat { background-repeat: repeat !important; }\n.d-bgr-space { background-repeat: space !important; }\n.d-bgr-none { background-repeat: no-repeat !important; }\n.d-bgr-unset { background-repeat: unset !important; }\n\n\n//  ============================================================================\n//  $  BACKGROUND SIZE\n//  ----------------------------------------------------------------------------\n.d-bgs-contain { background-size: contain !important; }\n.d-bgs-cover { background-size: cover !important; }\n.d-bgs-var { background-size: var(--bgg-size, 100% 100%) !important; }\n.d-bgs-auto { background-size: auto !important; }\n.d-bgs-unset { background-size: unset !important; }\n\n\n//  ============================================================================\n//  $   GRADIENTS\n//  ----------------------------------------------------------------------------\n.d-bgg-to-t { background-image: linear-gradient(to top, var(--bgg-from) 0%, var(--bgg-to) 100%) !important; }\n.d-bgg-to-tr { background